import { RoutingResponse } from 'types/models';
import useGetRequest from 'hooks/api/common/useGetRequest';
import { useQuery } from 'react-query';
import endpoints from 'api/endpoints';
import { AxiosError } from 'axios';

export const useRoutingv2Data = (
  step: string,
  onSuccess: (data: RoutingResponse) => void = () => {}
) => {
  const req = useGetRequest<RoutingResponse>();
  return useQuery<RoutingResponse, AxiosError<RoutingResponse>>(
    [endpoints.routing, step, 'v2'],
    () => {
      return req(`${endpoints.routing}/v2/${step}`, {}, true);
    },
    { onSuccess }
  );
};
