import React, { useMemo } from 'react';
import RoutingSelectMenuList, {
  ScrollableListItems,
} from './BaseSelectMenu/RoutingSelectMenuList';
import { RoutingSelectMenuProvider } from './ContextProvider/RoutingSelectMenuContextProvider';
import RoutingSelectMenuGroup from './BaseSelectMenu/RoutingSelectMenuGroup';
import MlModelSelectMenuGroup from './MLModelSelectMenuGroup';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import {
  useCreateRoutingGroupOptions,
  useRiskProfilesOptions,
  useRulesOptions,
  useAssignOutcomeOptions,
  useCreateMLModelOptions,
} from '../Hooks/useCreateRoutingGroupOptions';
import { groupsWithIndex } from '../routingUtils';
import RoutingSelectSubGroup from './BaseSelectMenu/RoutingSelectSubgroup';
import RoutingSelectMenuOptions from './BaseSelectMenu/RoutingSelectMenuOptions';
import RoutingDropdownButtons from './RoutingButtons/RoutingDropdownButtons';
import { useRoutingTree } from '../RoutingTreeContext';
import RoutingSelectMenuDeleteButton from './RoutingButtons/RoutingSelectMenuDeleteButton';
import { useDeleteRoutingElementModal } from '../RoutingContainer/DeleteRoutingElementContainer';
import { useTheme } from 'styled-components';
import { PaymentProcessingDecisions } from 'types/models';
import { getDecisionDisplayValue } from 'utils/decisions';
import { useRoutingNodeColor } from '../Hooks/useRoutingNodeColor';
import { useThreeDSVersion } from 'contexts/ThreeDSVersionContext';
import useMerchants from 'hooks/useMerchants';
import { useFeatureFlags } from 'contexts/FeatureFlagsContext';

type Props = {
  position: GridPosition;
  decision: PaymentProcessingDecisions;
  isHighlighted: boolean;
};

const getShortenedDisplayValue = (
  label: ReturnType<typeof getDecisionDisplayValue>
) => {
  switch (label) {
    case 'Force Challenge (liability shift)':
      return { label: 'Force Challenge', tooltip: 'Includes liability shift' };
    case 'No Preference (liability shift)':
      return { label: 'No Preference', tooltip: 'Includes liability shift' };
    case 'Try Frictionless (liability shift)':
      return {
        label: 'Try Frictionless',
        tooltip: 'Includes liability shift',
      };
    default:
      return { label, tooltip: 'Includes liability shift' };
  }
};

const OutcomeSelectMenu: React.FunctionComponent<Props> = ({
  position,
  decision,
  isHighlighted,
}) => {
  const {
    actions: { editNode },
  } = useRoutingTree();
  const { featureFlags } = useFeatureFlags();

  const { version } = useThreeDSVersion();

  const createOptions = useCreateRoutingGroupOptions(position, editNode);
  const outcomeOptions = useAssignOutcomeOptions(position, editNode);
  const riskProfilesOptions = useRiskProfilesOptions(position, editNode);
  const rulesOptions = useRulesOptions(position, editNode);
  const createMLModelOptions = useCreateMLModelOptions(position, editNode);
  const theme = useTheme();
  const [{ selectedMerchant }] = useMerchants();

  const { openModal } = useDeleteRoutingElementModal();

  const [
    createOptionsWithIndex,
    outcomesWithIndex,
    rulesOptionsWithIndex,
    riskProfilesOptionsWithIndex,
    mLModelOptionsWithIndex,
  ] = groupsWithIndex([
    createOptions,
    outcomeOptions,
    rulesOptions,
    riskProfilesOptions,
    createMLModelOptions,
  ]);

  const getNodeColor = useRoutingNodeColor();
  const nodeColor = getNodeColor(decision);

  const decisionDisplayValue = useMemo(
    () => getShortenedDisplayValue(getDecisionDisplayValue(decision, version)),
    [decision, version]
  );
  return (
    <RoutingSelectMenuProvider
      items={[
        ...createOptionsWithIndex,
        ...outcomesWithIndex,
        ...rulesOptionsWithIndex,
        ...riskProfilesOptionsWithIndex,
        ...mLModelOptionsWithIndex,
      ]}
    >
      <div>
        <RoutingDropdownButtons
          borderColor={isHighlighted ? theme.colors.focus : nodeColor}
          text={decisionDisplayValue.label}
          onLeftButtonClicked={() => {}}
          height={32}
          leftButtonStyle={{ cursor: 'default' }}
        />
        <RoutingSelectMenuList>
          <ScrollableListItems>
            <RoutingSelectMenuGroup title="Create">
              <RoutingSelectMenuOptions
                showIcon
                options={createOptionsWithIndex}
              />
            </RoutingSelectMenuGroup>
            <RoutingSelectMenuGroup title="Switch outcome">
              <RoutingSelectMenuOptions options={outcomesWithIndex} />
            </RoutingSelectMenuGroup>
            {(rulesOptionsWithIndex.length > 0 ||
              riskProfilesOptionsWithIndex.length > 0) && (
              <RoutingSelectMenuGroup title="Change to another element">
                <RoutingSelectSubGroup title="Your Rules">
                  <RoutingSelectMenuOptions options={rulesOptionsWithIndex} />
                </RoutingSelectSubGroup>
                <RoutingSelectSubGroup title="Your risk profiles">
                  <RoutingSelectMenuOptions
                    options={riskProfilesOptionsWithIndex}
                  />
                </RoutingSelectSubGroup>
              </RoutingSelectMenuGroup>
            )}
            {featureFlags.ROUTING_ML_MODULE &&
              selectedMerchant?.ml_score_enabled && (
                <MlModelSelectMenuGroup options={mLModelOptionsWithIndex} />
              )}
          </ScrollableListItems>
          <RoutingSelectMenuDeleteButton
            onDelete={() => openModal({ position, nodeType: 'outcome' })}
          />
        </RoutingSelectMenuList>
      </div>
    </RoutingSelectMenuProvider>
  );
};

export default OutcomeSelectMenu;
