import React from 'react';
import { Form, Formik } from 'formik';
import TextField from 'components/TextField';
import { Box, Flex, PrimaryButton, OutlineButton } from '@cko/primitives';
import addRiskProfileScoringRuleValidator from './AddRiskProfileScoringRuleValidator';
import FormikSelectMenu from 'components/FormikSelectMenu';
import { ScoringRuleSummary } from 'types/models';
import { AddScoredRuleActionPayload } from 'features/RiskProfiles/useRiskProfileState/riskProfileReducer';

interface Props {
  onCancel: () => void;
  onSubmit: (payload: AddScoredRuleActionPayload) => void;
  scoringRules: ScoringRuleSummary[];
}

type ScoringRuleSummaryWithoutTags = Omit<ScoringRuleSummary, 'tags'>;

const AddRiskProfileScoringRuleForm: React.FunctionComponent<Props> = ({
  onCancel,
  onSubmit,
  scoringRules,
}) => {
  return (
    <Formik
      initialValues={{
        score: '',
        rule: { id: '', name: '', tags: '', predicate: '' },
      }}
      onSubmit={(values) => {
        const { rule, score } = values;

        const payload = {
          score,
          scoring_rule_id: rule.id,
          name: rule.name,
        };

        onSubmit(payload);
      }}
      validateOnChange
      validate={addRiskProfileScoringRuleValidator}
    >
      {({ submitCount }) => (
        <Form>
          <Box mb={3}>
            <FormikSelectMenu<ScoringRuleSummaryWithoutTags>
              name="rule"
              options={scoringRules.map((scoringRule) => {
                const { tags, ...scoringRuleWithoutTags } = scoringRule;

                return {
                  label: scoringRuleWithoutTags.name,
                  value: { ...scoringRuleWithoutTags },
                };
              })}
              label="Rule"
              placeholder="Please select a rule"
            />
          </Box>
          <TextField
            label="Score"
            mb={4}
            name="score"
            type="number"
            submitCount={submitCount}
          />

          <Flex alignItems="center">
            <Box>
              <PrimaryButton data-cy="modal-add-rule" type="submit">
                Assign rule
              </PrimaryButton>
            </Box>
            <Box pl={3}>
              <OutlineButton onClick={onCancel}>Cancel</OutlineButton>
            </Box>
          </Flex>
        </Form>
      )}
    </Formik>
  );
};

export default AddRiskProfileScoringRuleForm;
