import React from 'react';
import { ClickableElement, Flex, Box } from '@cko/primitives';
import styled, { css } from 'styled-components';
import { IconActionSelect } from '@cko/icons';
import { useRoutingSelectMenuContext } from '../ContextProvider/RoutingSelectMenuContextProvider';
import LineClamp from 'components/LineClamp';
import { darken, lighten } from 'polished';
import usePermissions from 'hooks/usePermissions';
import { border } from 'styled-system';
import MLIcon from 'features/Routing/RoutingMap/ButtonIcons/MLIcon';

interface Props {
  onLeftButtonClicked: () => void;
  text: string;
  borderColor?: string;
  height?: number;
  leftButtonStyle?: React.CSSProperties;
  isMLNode?: boolean;
}

const LeftButton = styled(ClickableElement)<{ height: number }>(
  ({ theme, height }) => css`
    border-radius: 3px 0 0 3px;
    text-align: left;
    height: ${height}px;
    font-size: 12px;
    line-height: 16px;
    padding: 0 8px;
    width: 100%;
    background-color: ${lighten(0.08)(theme.colors.baseLight)};
    word-break: break-word;

    &:hover {
      background-color: ${darken(0.08)(theme.colors.baseLight)};
      border-radius: 3px 0 0 3px;
      text-align: left;
      z-index: 1000;
      color: ${theme.colors.focus};
      font-size: 12px;
      line-height: 16px;
      padding: 0 8px;
      width: 100%;
      word-break: break-word;
    }
  `
);

const LeftButtonWrapper = styled.div`
  flex-basis: 80%;
`;

const RightButton = styled(ClickableElement)(
  ({ theme }) => css`
    border-radius: 0 3px 3px 0px;
    background: ${theme.colors.baseLight};

    &:hover {
      background-color: ${darken(0.08)(theme.colors.baseLight)};
      border-radius: 0 3px 3px 0;
    }

    width: 100%;
    height: 100%;
    ${border}
  `
);

const RightButtonWrapper = styled.div`
  flex-basis: 20%;
`;

const IconSelectRouting = styled(IconActionSelect)`
  path {
    fill: ${({ theme }) => theme.colors.base};
  }
`;

const MLIconWrapper = styled(Box)`
  line-height: 12px;
`;

const RoutingDropdownButtons: React.FunctionComponent<Props> = ({
  onLeftButtonClicked,
  text,
  borderColor = '#C6D4DB',
  height = 42,
  leftButtonStyle,
  isMLNode = false,
}) => {
  const { getToggleButtonProps, popper } = useRoutingSelectMenuContext();
  const { hasPermission } = usePermissions();

  const hasWritePermissions = hasPermission('prism:routing:write');

  return (
    <Flex
      width="150px"
      border={`1px solid ${borderColor}`}
      borderRadius={1}
      backgroundColor="white"
      ref={popper.setReferenceElement}
    >
      <LeftButtonWrapper>
        <LeftButton
          style={leftButtonStyle}
          onClick={onLeftButtonClicked}
          disabled={!hasWritePermissions}
          title={text}
          height={height}
        >
          <Flex alignItems="center">
            {isMLNode && (
              <MLIconWrapper mr={1}>
                <MLIcon />
              </MLIconWrapper>
            )}
            <LineClamp>{text}</LineClamp>
          </Flex>
        </LeftButton>
      </LeftButtonWrapper>

      <RightButtonWrapper>
        <RightButton
          {...getToggleButtonProps()}
          borderLeft={`1px solid ${borderColor} !important`}
          disabled={!hasWritePermissions}
          data-cy="select-item-dropdown-button"
        >
          <IconSelectRouting />
        </RightButton>
      </RightButtonWrapper>
    </Flex>
  );
};
export default RoutingDropdownButtons;
