import { useMemo, useReducer } from 'react';
import { DynamicDecision, PaymentProcessingDecisions } from 'types/models';
import ScoringDecisionList from 'utils/ScoringDecisionList';
import decisionRangeReducer, {
  UpdateDecisionPayload,
} from './decisionRangeReducer';
import { decisionPriorityMap } from 'utils/decisions';

const sortByPriority = (decisions: DynamicDecision[]) =>
  decisions.sort(
    (x, y) => decisionPriorityMap[y.decision] - decisionPriorityMap[x.decision]
  );

const useDecisionRanges = (
  scoringDecisions: DynamicDecision[],
  setHasEdited: (hasEdited: boolean) => void
) => {
  const scoringDecisionsLinkedList = useMemo(
    () => new ScoringDecisionList(),
    []
  );

  const initialState = useMemo(() => {
    scoringDecisions.forEach((scoreDecision) => {
      scoringDecisionsLinkedList.add(scoreDecision);
    });

    return {
      decisions: sortByPriority(scoringDecisions),
    };
  }, [scoringDecisions, scoringDecisionsLinkedList]);

  const [state, dispatch] = useReducer(decisionRangeReducer, initialState);

  const actions = useMemo(() => {
    const actions = {
      handleDecisionChange: (payload: UpdateDecisionPayload) => {
        dispatch({
          type: 'UPDATE_DECISION',
          payload,
        });
        setHasEdited(true);
      },
      handleEnableDecision: (payload: PaymentProcessingDecisions) => {
        dispatch({
          type: 'ENABLE_DECISION',
          payload,
        });
        setHasEdited(true);
      },
      handleDisableDecision: (payload: PaymentProcessingDecisions) => {
        dispatch({
          type: 'DISABLE_DECISION',
          payload,
        });
        setHasEdited(true);
      },
    };

    return actions;
  }, [setHasEdited]);

  return {
    state,
    actions,
  };
};

export default useDecisionRanges;
