import React, { useMemo } from 'react';
import { Handle, Position } from 'react-flow-renderer';
import { useTheme } from 'styled-components';
import { ExtendedNodeProps } from 'types/models';
import FeatureFlag from 'components/FeatureFlag';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import { useRoutingTree } from '../RoutingTreeContext';
import IncomingArrow from './IncomingArrow';
import { isFirstNode } from '../routingUtils';
import RuleSelectMenu from '../RoutingSelectMenu/RuleSelectMenu';
import RoutingNode from './RoutingNode';

const RuleNode: React.FC<
  ExtendedNodeProps<{
    isHighlighted: boolean;
    rule_id: string;
    rule_name: string;
    rule_predicate: string;
    position: GridPosition;
  }>
> = ({ data, id }) => {
  const {
    state: { isHovering },
    rules,
  } = useRoutingTree();

  const theme = useTheme();

  const rule = useMemo(() => {
    return rules.find((rule) => rule.id === data.rule_id);
  }, [data.rule_id, rules]);

  return (
    <>
      <FeatureFlag on={isFirstNode(data.position)}>
        <IncomingArrow top="9px" left="-15px" />
      </FeatureFlag>
      <RoutingNode
        style={{
          opacity: isHovering && !data.isHighlighted ? 0.5 : 1,
        }}
        data-cy={`rule-node-${id}`}
        data-grid-position={`${data.position.col}:${data.position.row}`}
      >
        <Handle
          type="target"
          position={Position.Left}
          isConnectable={false}
          style={{ opacity: 0 }}
        />
        <Handle
          type="source"
          position={Position.Right}
          isConnectable={false}
          style={{ backgroundColor: theme.colors.base, top: 5 }}
          id={'a'}
        />
        <Handle
          type="source"
          position={Position.Right}
          isConnectable={false}
          style={{ backgroundColor: theme.colors.base, top: 27 }}
          id={'b'}
        />
        <RuleSelectMenu
          isHighlighted={data.isHighlighted}
          ruleName={rule?.name ?? data.rule_name}
          ruleId={rule?.id ?? data.rule_id}
          rulePredicate={rule?.predicate ?? data.rule_predicate}
          position={data.position}
        />
      </RoutingNode>
    </>
  );
};

export default RuleNode;
