import React from 'react';
import styled from 'styled-components';
import FocusLock, { AutoFocusInside } from 'react-focus-lock';

import { IconActionCross } from '@cko/icons';
import { Overlay, Portal, TextHeadingTwo, Flex, Box } from '@cko/primitives';
import { CloseButton } from 'features/RulesBuilder/RulesBuilderModal/ModalWithHeader';

export const Content = styled.div`
  border-radius: ${({ theme }) => theme.radii[1]};
  box-shadow: ${({ theme }) => theme.shadows.focusLight[3]};
  background: #fff;
  max-width: 744px;
  min-width: 200px;
  flex: 1;
  margin: 20px;
  padding: 40px;
  position: relative;
  border-top: 'success.2';
`;

interface ModalProp {
  title?: string;
  closeModal: () => void;
  focusWhitelist?: (el: HTMLElement) => boolean;
  headerContent: React.ReactNode;
}

const MLModalWithHeader: React.FC<ModalProp> = ({
  title,
  children,
  closeModal,
  focusWhitelist = () => false,
  headerContent,
}) => (
  <Portal>
    <Overlay
      onClick={(event: React.MouseEvent) => {
        event.stopPropagation();
        closeModal();
      }}
      onKeyUp={(event: React.KeyboardEvent) => {
        if (event.keyCode === 27) {
          event.stopPropagation();
          closeModal();
        }
      }}
      tabIndex={-1}
      role="dialog"
      aria-labelledby="modal-title"
      aria-describedby="modal-content"
      aria-modal="true"
    >
      <Content
        onClick={(event) => event.stopPropagation()}
        data-testid="modal-wrapper"
      >
        <FocusLock whiteList={focusWhitelist}>
          <CloseButton
            type="button"
            onClick={(event) => {
              event.stopPropagation();
              closeModal();
            }}
          >
            <IconActionCross />
          </CloseButton>
          <Flex alignItems="center" mb={2}>
            {title ? (
              <Box mr={2}>
                <TextHeadingTwo id="modal-title">{title}</TextHeadingTwo>
              </Box>
            ) : null}
            {headerContent}
          </Flex>

          <AutoFocusInside>
            <div id="modal-content" data-testid="modal-content">
              {children}
            </div>
          </AutoFocusInside>
        </FocusLock>
      </Content>
    </Overlay>
  </Portal>
);

export default MLModalWithHeader;
