import {
  RiskProfileResponse,
  RiskProfileScoredRule,
  ScoredRuleState,
} from 'types/models';

export type UpdateRangeEndActionPayload = number[];
export type AddRangeEndActionPayload = number;
export type DeleteRangeEndActionPayload = number;
export type LoadDataActionPayload = RiskProfileResponse;

export type UpdateScoredRuleActionPayload = {
  index: number;
  score: string;
};

export interface UpdateScoredRuleAction {
  type: 'UPDATE_SCORED_RULE';
  payload: UpdateScoredRuleActionPayload;
}

export type AddScoredRuleActionPayload = {
  score: string;
  scoring_rule_id: string;
  name: string;
};

export interface AddScoredRuleAction {
  type: 'ADD_SCORED_RULE';
  payload: AddScoredRuleActionPayload;
}

export interface RemoveScoredRuleSction {
  type: 'REMOVE_SCORED_RULE';
  payload: RiskProfileScoredRule;
}

export interface UpdateName {
  type: 'UPDATE_NAME';
  payload: string;
}

export type Action =
  | UpdateScoredRuleAction
  | AddScoredRuleAction
  | RemoveScoredRuleSction
  | UpdateName;

const reducer = (state: ScoredRuleState, action: Action): ScoredRuleState => {
  switch (action.type) {
    case 'UPDATE_SCORED_RULE':
      const { index, score } = action.payload;
      const scoredRuleToUpdate = state.scored_rules[index];

      return {
        ...state,
        scored_rules: state.scored_rules.map((scoredRule) => {
          if (
            scoredRuleToUpdate.scoring_rule_id === scoredRule.scoring_rule_id
          ) {
            return {
              ...scoredRuleToUpdate,
              score,
            };
          }

          return scoredRule;
        }),
      };

    case 'ADD_SCORED_RULE':
      return {
        ...state,
        scored_rules: [...state.scored_rules, action.payload],
      };

    case 'REMOVE_SCORED_RULE':
      return {
        ...state,
        scored_rules: state.scored_rules.filter(
          (scoredRule) =>
            scoredRule.scoring_rule_id !== action.payload.scoring_rule_id
        ),
      };

    case 'UPDATE_NAME': {
      return { ...state, name: action.payload };
    }

    default:
      return state;
  }
};

export default reducer;
