import { useQuery } from 'react-query';
import { RiskProfileResponse, PaymentProcessingStep } from 'types/models';
import useGetRequest from 'hooks/api/common/useGetRequest';
import endpoints from 'api/endpoints';
import { AxiosError } from 'axios';

export const useRiskProfileData = (
  paymentProcessingStep: PaymentProcessingStep,
  id?: string
) => {
  const req = useGetRequest<RiskProfileResponse>();
  return useQuery<RiskProfileResponse, AxiosError<RiskProfileResponse>>(
    [endpoints.riskProfiles, id],
    () => req(`${endpoints.riskProfilesStep(paymentProcessingStep)}/${id}`),
    {
      enabled: !!id,
    }
  );
};
