import React, { useMemo } from 'react';
import { Box, Flex, SecondaryButton } from '@cko/primitives';
import { IconActionAdd } from '@cko/icons';
import { RiskProfileScoredRule, ScoringRuleSummary } from 'types/models';
import RiskProfileScoringRulesTable from 'components/RiskProfileScoringRulesTable';
import { HandleScoredRuleUpdate } from 'components/RiskProfileScoringRulesTable/RiskProfileScoringRulesTable';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import AddRiskProfilesScoringRuleContainer from './AddRiskProfilesScoringRuleContainer';
import TooltipButtonWrapper from 'components/TooltipButtonWrapper';
import usePermissions from 'hooks/usePermissions';
import { AddScoredRuleActionPayload } from './useRiskProfileState/riskProfileReducer';
import styled from 'styled-components';

interface Props {
  scoredRules: RiskProfileScoredRule[];
  handleScoredRuleUpdate: HandleScoredRuleUpdate;
  addScoringRule: (payload: AddScoredRuleActionPayload) => void;
  handleDeleteRiskProfileRule: (rule: RiskProfileScoredRule) => void;
  rules: ScoringRuleSummary[];
  riskProfileName: string;
}

const AssignIcon = styled(IconActionAdd)(
  ({ theme }) => `
margin-right: ${theme.space[1]};
`
);

const addScoringRuleTooltipMessage = (
  rules: ScoringRuleSummary[],
  filteredRules: ScoringRuleSummary[]
) => {
  if (rules.length === 0) {
    return 'No available rules, please create one';
  }

  if (filteredRules.length === 0) {
    return 'All available rules have been attached to this risk profile';
  }

  return null;
};

const RiskProfilesScoringRulesContainer: React.FunctionComponent<Props> = ({
  scoredRules,
  handleScoredRuleUpdate,
  addScoringRule,
  handleDeleteRiskProfileRule,
  rules,
  riskProfileName,
}) => {
  const { openModal } = useModalManager<
    Omit<ScoringRuleSummary, 'risk_profiles'>[]
  >(Modals.ADD_RISK_PROFILE_SCORING_RULE);

  const filteredRules = useMemo(() => {
    const selectedIds = scoredRules.map(
      (scoredRule) => scoredRule.scoring_rule_id
    );

    return rules
      .filter((rule) => {
        return !selectedIds.includes(rule.id);
      })
      .map(({ risk_profiles, ...rule }) => rule);
  }, [rules, scoredRules]);

  const addScoringRuleMessage = addScoringRuleTooltipMessage(
    rules,
    filteredRules
  );
  const { hasPermission } = usePermissions();

  return (
    <>
      <Box mb={2}>
        <Flex p={3} justifyContent="space-between" alignItems="center">
          <h1>Scoring rules</h1>
          <TooltipButtonWrapper
            enabled={addScoringRuleMessage !== null}
            text={addScoringRuleMessage || ''}
          >
            {hasPermission('prism:risk-profile:write') && (
              <SecondaryButton
                disabled={rules.length === 0 || filteredRules.length === 0}
                style={{
                  pointerEvents:
                    addScoringRuleMessage !== null ? 'none' : 'auto',
                }}
                onClick={() => {
                  openModal(filteredRules);
                }}
              >
                <AssignIcon />
                Assign rule to {riskProfileName}
              </SecondaryButton>
            )}
          </TooltipButtonWrapper>
        </Flex>
        <RiskProfileScoringRulesTable
          scoredRules={scoredRules}
          handleScoredRuleUpdate={handleScoredRuleUpdate}
          handleDeleteRiskProfileRule={handleDeleteRiskProfileRule}
        />
      </Box>
      <AddRiskProfilesScoringRuleContainer
        addScoringRule={addScoringRule}
        riskProfileName={riskProfileName}
      />
    </>
  );
};

export default RiskProfilesScoringRulesContainer;
