import { PaymentProcessingStep } from 'types/models';
import { useUpdateV2Routing } from 'hooks/api/routing';
import RoutingGrid from '../RoutingGrid';
import { denormalizeGrid } from '../routingUtils/transformRoutingData';
import useAlerts from 'hooks/useAlerts';
import { useCallback } from 'react';

const useSaveRoutingTree = (
  step: PaymentProcessingStep,
  routingGrid: RoutingGrid
) => {
  const { showAlert } = useAlerts();

  const [updateRouting] = useUpdateV2Routing(step, {
    onSuccess: () => {
      showAlert('Successfully saved routing data');
    },
    onError: () => {
      showAlert('Error saving routing data', { type: 'alert' });
    },
  });

  const handleSave = useCallback(() => {
    return updateRouting({
      data: {
        routing: denormalizeGrid(routingGrid),
      },
    });
  }, [routingGrid, updateRouting]);

  const checkRoutingTree = useCallback(() => {
    try {
      denormalizeGrid(routingGrid);
      return true;
    } catch {
      return false;
    }
  }, [routingGrid]);

  return { handleSave, checkRoutingTree };
};

export default useSaveRoutingTree;
