import React from 'react';
import {
  Modal,
  Box,
  Text,
  Flex,
  DangerButton,
  OutlineButton,
} from '@cko/primitives';
import { RoutingNodeType } from 'types/models';
import { isTerminatingNode } from 'features/Routing/routingUtils';

interface Props {
  handleClose: () => void;
  onDelete: () => void;
  nodeType: RoutingNodeType;
}

const displayNodeTypeName: Record<RoutingNodeType, string> = {
  rule: 'rule',
  risk_profile: 'risk profile',
  outcome: 'outcome',
  rule_group: 'rule group',
  addFirstElement: 'add first element',
  addNextElement: 'add element',
  add_elements: 'add elements',
  machine_learning: 'machine learning model',
};

const RoutingDeleteElementModal: React.FunctionComponent<Props> = ({
  handleClose,
  onDelete,
  nodeType,
}) => {
  const nodeDisplayValue = displayNodeTypeName[nodeType];
  return (
    <Modal
      type="alert"
      title={
        isTerminatingNode(nodeType)
          ? `You are deleting this ${nodeDisplayValue}`
          : `You are deleting a ${nodeDisplayValue} which has elements connected to it`
      }
      closeModal={handleClose}
      size="large"
    >
      <Box pb={4}>
        <Text fontSize={0} pb={3}>
          {isTerminatingNode(nodeType)
            ? `Do you wish to delete this ${nodeDisplayValue}?`
            : `Do you wish to delete this ${nodeDisplayValue} and everything connected to it?`}
        </Text>
        <Text fontSize={0} fontWeight={400} color="#BE4343">
          This action is permanent.
        </Text>
      </Box>
      <Flex alignItems="center" p={0}>
        <Box>
          <DangerButton onClick={() => onDelete()} data-testid="delete-button">
            {isTerminatingNode(nodeType)
              ? `Delete ${nodeDisplayValue}`
              : `Delete ${nodeDisplayValue} and all following elements`}
          </DangerButton>
        </Box>
        <Box pl={3}>
          <OutlineButton onClick={handleClose}>Cancel</OutlineButton>
        </Box>
      </Flex>
    </Modal>
  );
};

export default RoutingDeleteElementModal;
