import React, { memo, useRef, useState, useLayoutEffect } from 'react';
import {
  getSmoothStepPath,
  EdgeProps,
  Position,
  getEdgeCenter,
  EdgeTextProps,
  Rect,
  ArrowHeadType,
} from 'react-flow-renderer';
import styled from 'styled-components';

const Text = styled.text`
  stroke: ${({ theme }) => theme.colors.white};
  font-size: 12px;
  pointer-events: none;
  user-select: none;
`;

const TextCircle = styled.circle`
  fill: ${({ theme }) => theme.colors.base};
`;

const CustomEdgeText = memo(({ x, y, label }: EdgeTextProps) => {
  const edgeRef = useRef<SVGTextElement>(null);
  const [edgeTextBbox, setEdgeTextBbox] = useState<Rect>({
    x: 0,
    y: 0,
    width: 0,
    height: 0,
  });

  useLayoutEffect(() => {
    if (edgeRef.current) {
      const textBbox = edgeRef.current.getBBox();

      setEdgeTextBbox({
        x: textBbox.x,
        y: textBbox.y,
        width: textBbox.width,
        height: textBbox.height,
      });
    }
  }, []);

  if (typeof label === 'undefined' || !label) {
    return null;
  }

  return (
    <g
      transform={`translate(${x - edgeTextBbox.width / 2} ${
        y - edgeTextBbox.height / 2
      })`}
    >
      <TextCircle
        cx={edgeTextBbox.width / 2}
        cy={edgeTextBbox.height / 2}
        r={8}
      />
      <Text y={edgeTextBbox.height / 2} dy="0.35em" ref={edgeRef} fill="white">
        {label}
      </Text>
    </g>
  );
});

const Path = styled.path`
  fill: none;
  stroke: #778293;
`;

const RoutingEdge: React.FC<EdgeProps> = ({
  sourceX,
  sourceY,
  targetX,
  targetY,
  label,
  sourcePosition = Position.Bottom,
  targetPosition = Position.Top,
  arrowHeadType,
}) => {
  const [centerX] = getEdgeCenter({
    sourceX,
    sourceY,
    targetX,
    targetY,
  });

  const newCenterX = centerX + 8;
  const newsourceX = sourceX - 4;

  const path = getSmoothStepPath({
    sourceX: sourceX - 4,
    sourceY,
    sourcePosition,
    targetX: targetX + 4,
    targetY,
    targetPosition,
    borderRadius: 2,
    centerX: newCenterX,
  });

  const text = label ? (
    <CustomEdgeText
      x={newsourceX + (newCenterX - newsourceX) / 2}
      y={sourceY}
      label={label}
    />
  ) : null;

  return (
    <>
      <defs>
        <marker
          id="triangle"
          viewBox="0 0 6 12"
          refX="6"
          refY="6"
          markerUnits="strokeWidth"
          markerWidth="6"
          markerHeight="12"
          orient="auto"
        >
          <path
            d="M6 6L-1.25622e-08 11.1962L4.417e-07 0.803848L6 6Z"
            fill="#778293"
          />
        </marker>
      </defs>
      <Path
        d={path}
        markerEnd={
          arrowHeadType === ArrowHeadType.ArrowClosed
            ? 'url(#triangle)'
            : 'none'
        }
      />
      {text}
    </>
  );
};

export default RoutingEdge;
