import React from 'react';
import { Box, ClickableElement, Flex } from '@cko/primitives';
import { IconActionAdd } from '@cko/icons';
import { useZoomPanHelper } from 'react-flow-renderer';
import 'styled-components/macro';
import styled from 'styled-components/macro';
import ZoomOutIcon from './ButtonIcons/zoomOutIcon';
import RecenterIcon from './ButtonIcons/RecenterIcon';

const ButtonGroup = styled(Flex)`
  &:hover {
    border-color: ${({ theme }) => theme.colors.successDark};
  }

  button {
    :hover {
      border-color: ${({ theme }) => theme.colors.successDark};
    }
  }

  button:first-child {
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  button:not(:first-child) {
    border-radius: 0;
  }

  button:last-child {
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
  }
`;

const IconActionAddSuccess = styled(IconActionAdd)`
  & > g {
    fill: ${({ theme }) => theme.colors.successDark};
  }
`;

const ControlButton = styled(ClickableElement)`
  height: 20px;
  width: 20px;
  padding: 1px;
  border: solid 1px ${({ theme }) => theme.colors.baseLight};
`;

const ButtonWrapper = styled(Box)`
  position: absolute;
  right: 18px;
  top: 15px;
  z-index: 5;
`;

const MiniMapControls: React.FunctionComponent = () => {
  const { fitView, zoomIn, zoomOut } = useZoomPanHelper();

  return (
    <ButtonWrapper>
      <ButtonGroup
        flexDirection="column"
        justifyContent="center"
        alignContent="center"
      >
        <ControlButton
          onClick={() => {
            zoomIn();
          }}
        >
          <IconActionAddSuccess />
        </ControlButton>
        <ControlButton
          onClick={() => {
            zoomOut();
          }}
        >
          <ZoomOutIcon />
        </ControlButton>
        <ControlButton onClick={() => fitView({ padding: 0.1 })}>
          <RecenterIcon />
        </ControlButton>
      </ButtonGroup>
    </ButtonWrapper>
  );
};

export default MiniMapControls;
