import {
  RiskProfileResponse,
  PaymentProcessingDecisions,
  DynamicDecision,
  DecisionRangeState,
} from 'types/models';
import ScoringDecisionList from 'utils/ScoringDecisionList';
import { PartialBy } from 'types/utilities';

export type UpdateRangeEndActionPayload = number[];
export type AddRangeEndActionPayload = number;
export type DeleteRangeEndActionPayload = number;
export type LoadDataActionPayload = RiskProfileResponse;

export type UpdateDecisionPayload = {
  values: number[];
  decision: PaymentProcessingDecisions;
};

export type UpdateScoredRuleActionPayload = {
  index: number;
  score: string;
};

export interface UpdateDecision {
  type: 'UPDATE_DECISION';
  payload: UpdateDecisionPayload;
}

export interface EnableDecision {
  type: 'ENABLE_DECISION';
  payload: PaymentProcessingDecisions;
}

export interface DisableDecision {
  type: 'DISABLE_DECISION';
  payload: PaymentProcessingDecisions;
}

export type Action = UpdateDecision | EnableDecision | DisableDecision;

const decisionRangeReducer = (
  state: DecisionRangeState,
  action: Action
): DecisionRangeState => {
  const scoringDecisionsLinkedList = new ScoringDecisionList();

  state.decisions.forEach((scoreDecision) => {
    scoringDecisionsLinkedList.add(scoreDecision as DynamicDecision);
  });

  switch (action.type) {
    case 'UPDATE_DECISION': {
      const { decision, values } = action.payload;

      const [start, end] = values;
      const decisionNode = scoringDecisionsLinkedList.findByDecision(decision);

      if (!decisionNode) {
        return state;
      }

      if (decisionNode.prevEnd === null) {
        if (decisionNode.nextEnd && end >= decisionNode.nextEnd.value.end - 1) {
          return state;
        }

        decisionNode.value.end = end > 0 ? end : 1;
      } else if (decisionNode.prevEnd && decisionNode.nextEnd) {
        if (
          (decisionNode.prevEnd.prevEnd &&
            start <= decisionNode.prevEnd.prevEnd.value.end + 2) ||
          (decisionNode.nextEnd && end > decisionNode.nextEnd.value.end - 2) ||
          start === end
        ) {
          return state;
        }

        decisionNode.prevEnd.value.end = start < 2 ? 1 : start - 1;
        decisionNode.value.end = end > 98 ? 98 : end;
      } else {
        const overlapsWithStartOfNextDecision =
          decisionNode.prevEnd.prevEnd &&
          start <= decisionNode.prevEnd.prevEnd.value.end + 2;
        const overlapsWithFirstDecision =
          !decisionNode.prevEnd.prevEnd && start < 2;

        const startEndOverlap = start >= 100;

        if (
          overlapsWithStartOfNextDecision ||
          overlapsWithFirstDecision ||
          startEndOverlap
        ) {
          return state;
        }

        decisionNode.prevEnd.value.end = start - 1;
      }

      return {
        ...state,
        decisions: scoringDecisionsLinkedList.toArray(),
      };
    }

    case 'ENABLE_DECISION': {
      const offset = 5;

      const decisionNode = scoringDecisionsLinkedList.findByDecision(
        action.payload
      );

      if (decisionNode === null) {
        return state;
      }

      const prevEndNode = decisionNode.findPrevEndNode();
      const nextEndNode = decisionNode.findNextEndNode();

      const setNodes = () => {
        decisionNode.prevEnd = prevEndNode;
        decisionNode.prev = prevEndNode;
        decisionNode.nextEnd = nextEndNode;
        if (prevEndNode) {
          prevEndNode.nextEnd = decisionNode;
        }
      };

      if (prevEndNode === null) {
        if (!nextEndNode) {
          return state;
        }

        if (nextEndNode.value.end <= offset) {
          decisionNode.value.end = 1;
        } else {
          decisionNode.value.end = offset;
        }

        nextEndNode.prevEnd = decisionNode;
        decisionNode.nextEnd = nextEndNode;

        decisionNode.pushOverlappingNextNodes();
      } else if (nextEndNode === null) {
        decisionNode.value.end = 100;
        prevEndNode.value.end = prevEndNode.value.end - 2;

        setNodes();

        decisionNode.fixPreviousOverlappingNodes();
      } else {
        if (
          (prevEndNode.prevEnd && !prevEndNode.touchingPreviousNode()) ||
          (!prevEndNode.prevEnd && prevEndNode.value.end > 2)
        ) {
          decisionNode.value.end = prevEndNode.value.end;
          prevEndNode.value.end = prevEndNode.value.end - 2;

          setNodes();

          if (nextEndNode) {
            nextEndNode.prevEnd = decisionNode;
            nextEndNode.prev = decisionNode;
          }
        } else if (!prevEndNode.previousNodesTouching()) {
          decisionNode.value.end = prevEndNode.value.end;
          prevEndNode.value.end = decisionNode.value.end - 2;

          setNodes();

          if (nextEndNode) {
            nextEndNode.prevEnd = decisionNode;
            nextEndNode.prev = decisionNode;
          }

          decisionNode.fixPreviousOverlappingNodes();
        } else {
          const newEnd = prevEndNode.value.end + 2;
          decisionNode.value.end = newEnd;

          setNodes();

          if (nextEndNode) {
            nextEndNode.prevEnd = decisionNode;
            nextEndNode.prev = decisionNode;
          }

          if (newEnd > prevEndNode.value.end) {
            decisionNode.pushOverlappingNextNodes();
          }
        }
      }

      return {
        ...state,
        decisions: scoringDecisionsLinkedList.toArray(),
      };
    }

    case 'DISABLE_DECISION': {
      const decisionNode = scoringDecisionsLinkedList.findByDecision(
        action.payload
      );

      if (decisionNode === null) {
        return state;
      }

      const prevEndNode = decisionNode.findPrevEndNode();

      const nextEndNode = decisionNode.findNextEndNode();

      if (!prevEndNode && nextEndNode) {
        nextEndNode.prevEnd = null;
      } else if (!nextEndNode && prevEndNode) {
        prevEndNode.nextEnd = null;
        prevEndNode.value.end = decisionNode.value.end;
      } else {
        if (prevEndNode) {
          prevEndNode.nextEnd = nextEndNode;
          prevEndNode.value.end = decisionNode.value.end;
        }

        if (nextEndNode) {
          nextEndNode.prevEnd = prevEndNode;
        }
      }

      delete (decisionNode.value as PartialBy<DynamicDecision, 'end'>).end;

      return {
        ...state,
        decisions: scoringDecisionsLinkedList.toArray(),
      };
    }

    default:
      return state;
  }
};

export default decisionRangeReducer;
