import React from 'react';
import { Box } from '@cko/primitives';
import { Handle, Position } from 'react-flow-renderer';
import { NextElementSelectMenu } from '../RoutingSelectMenu';

import { ExtendedNodeProps } from 'types/models';
import { GridPosition } from '../RoutingGrid/RoutingGrid';

const AddNode: React.FC<React.HTMLAttributes<HTMLDivElement>> = ({
  children,
  ...rest
}) => {
  return (
    <Box width="150px" {...rest}>
      <Handle
        type="target"
        position={Position.Left}
        isConnectable={false}
        style={{ opacity: 0 }}
      />
      <Handle
        type="source"
        position={Position.Right}
        isConnectable={false}
        style={{ opacity: 0, top: 5 }}
        id={'a'}
      />
      <Handle
        type="source"
        position={Position.Right}
        isConnectable={false}
        style={{ opacity: 0, top: 27 }}
        id={'b'}
      />
      {children}
    </Box>
  );
};

const AddNextElementNode: React.FC<
  ExtendedNodeProps<{
    position: GridPosition;
  }>
> = ({ id, data }) => {
  return (
    <AddNode
      data-cy={`add-next-element-node-${id}`}
      data-grid-position={`${data.position.col}:${data.position.row}`}
    >
      <NextElementSelectMenu position={data.position} />
    </AddNode>
  );
};

export default AddNextElementNode;
