import endpoints from 'api/endpoints';
import { useAddUpdateMutation } from '../common/useAddUpdateMutation';
import { CreateRiskProfileFormData } from 'forms/CreateRiskProfileForm/CreateRiskProfileForm';
import { PaymentProcessingStep } from 'types/models';

export const useEditRiskProfile = (
  paymentProcessingStep: PaymentProcessingStep,
  onSuccess: () => void = () => {}
) => {
  return useAddUpdateMutation<CreateRiskProfileFormData, { id: string }>(
    'put',
    endpoints.riskProfilesStep(paymentProcessingStep),
    {
      onSuccess,
    }
  );
};
