import React, { useCallback } from 'react';
import { TableCell, SwitchButton } from '@cko/primitives';
import DecisionSlider from '../DecisionSlider';
import {
  PaymentProcessingStep,
  PaymentProcessingDecisions,
} from 'types/models';
import usePermissions from 'hooks/usePermissions';
import TooltipButtonWrapper from 'components/TooltipButtonWrapper';
import InfoTooltip from 'components/InfoTooltip';
import { getDecisionMap } from 'utils/decisions';
import { UpdateDecisionPayload } from 'types/models';
import { ThreeDSVersion } from 'contexts/ThreeDSVersionContext';

interface Props {
  decision: PaymentProcessingDecisions;
  start: number;
  end?: number;
  handleUpdateScoringRange: (payload: UpdateDecisionPayload) => void;
  handleEnableDecision: (payload: PaymentProcessingDecisions) => void;
  handleDisableDecision: (payload: PaymentProcessingDecisions) => void;
  enabledCount: number;
  paymentProcessingStep: PaymentProcessingStep;
}

const DecisionRow: React.FunctionComponent<Props> = ({
  decision,
  start,
  end,
  handleUpdateScoringRange,
  handleEnableDecision,
  handleDisableDecision,
  enabledCount,
  paymentProcessingStep,
}) => {
  const enabled = end !== undefined;

  const decisionMap = getDecisionMap(paymentProcessingStep, ThreeDSVersion.Two);

  const handleSliderChange = useCallback(
    (values: number[]) => {
      return handleUpdateScoringRange({ decision, values });
    },
    [decision, handleUpdateScoringRange]
  );

  const { hasPermission } = usePermissions();

  return (
    <tr>
      <TableCell>
        <TooltipButtonWrapper
          enabled={enabled && enabledCount <= 2}
          text={'At least two scoring decisions must be enabled'}
        >
          <SwitchButton
            id={decision}
            data-testid="decision-row-switch-button"
            checked={enabled}
            onChange={() =>
              enabled
                ? handleDisableDecision(decision)
                : handleEnableDecision(decision)
            }
            disabled={
              (enabled && enabledCount <= 2) ||
              !hasPermission('prism:risk-profile:write')
            }
          />
        </TooltipButtonWrapper>
      </TableCell>
      <TableCell>
        {decisionMap.displayValues[decision as PaymentProcessingDecisions]}{' '}
        {decisionMap.descriptions[decision as PaymentProcessingDecisions] !==
          '' && (
          <InfoTooltip
            text={
              decisionMap.descriptions[decision as PaymentProcessingDecisions]
            }
          />
        )}
      </TableCell>
      <TableCell id={`${decision}_sliders`}>
        {enabled && (
          <DecisionSlider
            disabled={!hasPermission('prism:risk-profile:write')}
            start={start}
            end={end}
            onChange={handleSliderChange}
          />
        )}
      </TableCell>
    </tr>
  );
};

export default DecisionRow;
