import { useQuery } from 'react-query';
import {
  RiskProfileSummariesResponse,
  PaymentProcessingStep,
} from 'types/models';
import useGetRequest from 'hooks/api/common/useGetRequest';
import endpoints from 'api/endpoints';
import { AxiosError } from 'axios';

export const useRiskProfileSummariesData = (
  paymentProcessingStep: PaymentProcessingStep
) => {
  const req = useGetRequest<RiskProfileSummariesResponse>();

  return useQuery<
    RiskProfileSummariesResponse,
    AxiosError<RiskProfileSummariesResponse>
  >([endpoints.riskProfiles, paymentProcessingStep], () =>
    req(endpoints.riskProfilesStep(paymentProcessingStep))
  );
};
