import React from 'react';
import ErrorBoundary, { LoggingErrorBoundary } from 'components/ErrorBoundary';
import { ErrorPanel } from 'components/ErrorScreen/ErrorScreen';
import { Flex, Text, PrimaryButton, Link, Box } from '@cko/primitives';
import { useRoutingTree } from '../RoutingTreeContext';

const RoutingErrorScreen: React.FC = () => {
  const onClick = () => {
    window.location.reload();
  };

  return (
    <Flex py={5} justifyContent="center">
      <ErrorPanel>
        <Text fontSize={1} lineHeight={1}>
          Something went wrong. You can reset your routing configuration by
          refreshing or you can get in touch with the support team at:{' '}
          <Link href="mailto:prism-support@checkout.com">
            prism-support@checkout.com
          </Link>
        </Text>

        <Box mt={3}>
          <PrimaryButton onClick={onClick}>Refresh</PrimaryButton>
        </Box>
      </ErrorPanel>
    </Flex>
  );
};

const RoutingErrorBoundary: React.FC = ({ children }) => {
  const { state, step, routingGrid } = useRoutingTree();

  return (
    <ErrorBoundary fallback={<RoutingErrorScreen />}>
      <LoggingErrorBoundary data={{ state, step, routingGrid }}>
        {children}
      </LoggingErrorBoundary>
    </ErrorBoundary>
  );
};

export default RoutingErrorBoundary;
