import React, { useCallback } from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import { Modal } from '@cko/primitives';
import { PaymentProcessingStep, FormMode } from 'types/models';
import { RiskProfileMetaData } from '../RiskProfileFormContainer/RiskProfileFormModal';
import CreateRiskProfileForm from 'forms/CreateRiskProfileForm';
import { useCreateRiskProfile } from 'hooks/api/riskProfiles';
import endpoints from 'api/endpoints';
import { queryCache } from 'react-query';
import { useRoutingRiskProfileModal } from '../RiskProfileFormContainer';

export const useCreateRoutingRiskProfileModal = () => {
  return useModalManager<{
    formMode: FormMode;
    onRequestSuccess?: (data: RiskProfileMetaData) => void;
  }>(Modals.ROUTING_CREATE_RISK_PROFILE_FORM);
};

const CreateRiskProfileFormContainer: React.FC<{
  step: PaymentProcessingStep;
}> = ({ step }) => {
  const { closeModal, modalData } = useCreateRoutingRiskProfileModal();
  const { openModal } = useRoutingRiskProfileModal();

  const handleClose = useCallback(() => {
    closeModal();
  }, [closeModal]);

  const handleSuccess = (id: string) => {
    queryCache.invalidateQueries([endpoints.riskProfiles, step]);
    openModal({
      formMode: FormMode.Edit,
      id,
      onRequestSuccess: modalData.onRequestSuccess,
    });
    handleClose();
  };

  const [createRiskProfile] = useCreateRiskProfile(step, handleSuccess);

  if (!modalData) {
    return null;
  }

  return (
    <Modal closeModal={handleClose}>
      <CreateRiskProfileForm
        initialValues={{ name: '' }}
        onSubmit={createRiskProfile}
        onCancel={handleClose}
      />
    </Modal>
  );
};

export default CreateRiskProfileFormContainer;
