import React, { useState } from 'react';
import { PrimaryButton, Flex, InlineNotification, Box } from '@cko/primitives';
import { useRoutingTree } from '../RoutingTreeContext';

interface RoutingAlert {
  type: 'success' | 'alert' | 'warning';
  message: string;
}

const RoutingSaveButtons = () => {
  const { handleSave, checkRoutingTree, actions } = useRoutingTree();
  const [canSave, setCanSave] = useState(false);
  const [routingAlert, setRoutingAlert] = useState<RoutingAlert | null>(null);

  return (
    <Flex justifyContent="flex-start">
      <Box height="32px" pr={3}>
        <PrimaryButton
          onClick={() => {
            const isRoutingTreeValid = checkRoutingTree();
            setCanSave(isRoutingTreeValid);
            !isRoutingTreeValid
              ? setRoutingAlert({
                  type: 'alert',
                  message:
                    'Sorry, you have incomplete branches, you need to assign elements or outcomes to these in order to save your routing.',
                })
              : setRoutingAlert({
                  type: 'success',
                  message: 'Routing is completed.',
                });
          }}
        >
          Check routing tree
        </PrimaryButton>
      </Box>
      <Box height="32px" pr={3}>
        <PrimaryButton
          onClick={async () => {
            try {
              await handleSave();
              setRoutingAlert(null);
              setCanSave(false);
              actions.saveTree();
            } catch {}
          }}
          disabled={!canSave}
        >
          Save and put live
        </PrimaryButton>
      </Box>
      {routingAlert !== null && (
        <Box width="300px">
          <InlineNotification type={routingAlert.type}>
            {routingAlert.message}
          </InlineNotification>
        </Box>
      )}
    </Flex>
  );
};

export default RoutingSaveButtons;
