import { RoutingNodeType } from 'types/models';

import createRoutingNodeId from './../routingUtils/createRoutingNodeId';

class RoutingGridItem {
  public id: string;
  public type: RoutingNodeType;
  public data: any;
  public parent: RoutingGridItem | null;
  public true: RoutingGridItem | null;
  public false: RoutingGridItem | null;

  constructor(type: RoutingNodeType, data: any = {}) {
    this.id = createRoutingNodeId();
    this.type = type;
    this.data = data;
    this.parent = null;
    this.true = null;
    this.false = null;
  }

  visitChildBranches(
    visitor: (item: RoutingGridItem, isTrueNode?: boolean) => void
  ) {
    const visitBranches = (item: RoutingGridItem) => {
      if (item.true !== null) {
        visitor(item.true, true);
        visitBranches(item.true);
      }

      if (item.false !== null) {
        visitor(item.false, false);
        visitBranches(item.false);
      }
    };

    visitBranches(this);
  }
}

export default RoutingGridItem;
