import { Node, Position, Edge, ArrowHeadType } from 'react-flow-renderer';
import RoutingGridItem from './RoutingGridItem';
import createRoutingNodeId from './../routingUtils/createRoutingNodeId';
import { RoutingNodeType, RoutingTreeNode } from 'types/models';
import RoutingTreeGrid from './RoutingTreeGrid';

export type GridPosition = {
  row: number;
  col: number;
};

class RoutingGrid {
  public grid: Array<Array<RoutingGridItem | null>>;
  public size: number;
  public positionsById: Record<string, GridPosition>;

  constructor(size: number) {
    this.grid = Array.from({ length: size }, () =>
      Array.from({ length: size }, () => null)
    );
    this.size = size;
    this.positionsById = {};
  }

  private createRoutingTree(item: RoutingGridItem): RoutingTreeNode {
    let root = item;
    while (root.parent != null) {
      root = root.parent;
    }

    const result = this.wrapInRoutingTree(root);
    if (result != null) {
      return result;
    }

    return {
      data: root,
      children: [],
    };
  }

  private wrapInRoutingTree(item: RoutingGridItem): RoutingTreeNode {
    const result: RoutingTreeNode = {
      data: item,
      children: [],
    };

    if (item.true != null) {
      result.children.push(this.wrapInRoutingTree(item.true));
    }

    if (item.false != null) {
      result.children.push(this.wrapInRoutingTree(item.false));
    }

    return result;
  }

  isEmpty() {
    return this.getItem({ row: 0, col: 0 }) === null;
  }

  getItem({ row, col }: GridPosition): RoutingGridItem | null {
    return this.grid[row][col] || null;
  }

  getRow(row: number) {
    return this.grid[row];
  }

  fromTree(node: RoutingGridItem) {
    const newRoutingTreeState = this.createRoutingTree(node);
    const routingTreeGrid = new RoutingTreeGrid();
    routingTreeGrid.setTree(newRoutingTreeState);
    const newGrid = routingTreeGrid.getGrid();

    this.grid = newGrid.map((row) => {
      return row.map((colItem) => {
        if (colItem == null) {
          return null;
        }

        return colItem.data;
      });
    });

    this.visitGridItems((gridItem, gridPosition) => {
      this.positionsById[gridItem.id] = gridPosition;
    });

    return this.grid;
  }

  visitGridItems(
    vistor: (item: RoutingGridItem, gridPosition: GridPosition) => void
  ) {
    this.grid.forEach((_, row) => {
      this.grid[row].forEach((_, col) => {
        const gridPosition = { row, col };

        const item = this.getItem(gridPosition);

        if (item) {
          vistor(item, gridPosition);
        }
      });
    });
  }

  createNodes() {
    const nodes: Node[] = [];

    const positionOffset = {
      x: 195,
      y: 52,
    };

    this.visitGridItems((item, gridPosition) => {
      const nodeVerticalOffset = 11 * gridPosition.col;

      const node = {
        id: item.id,
        type: item.type,
        sourcePosition: Position.Right,
        targetPosition: Position.Left,
        position: {
          x: positionOffset.x * gridPosition.col,
          y: positionOffset.y * gridPosition.row - nodeVerticalOffset,
        },
        data: {
          ...(item.data || {}),
          position: gridPosition,
        },
      };

      nodes.push(node);
    });

    return nodes;
  }

  createEdges() {
    const edges: Edge[] = [];

    this.visitGridItems((item) => {
      if (item.true) {
        edges.push({
          id: createRoutingNodeId(),
          source: `${item.id}`,
          sourceHandle: 'a',
          type: 'custom',
          target: item.true.id,
          label: item.type !== 'addFirstElement' ? 'T' : '',
          arrowHeadType: getArrow(item.true.type),
          animated: item.true.data.isHighlighted,
          data: { isHighlighted: item.true.data.isHighlighted },
        });
      }

      if (item.false) {
        edges.push({
          id: createRoutingNodeId(),
          source: `${item.id}`,
          sourceHandle: 'b',
          type: 'custom',
          target: item.false.id,
          label: 'F',
          arrowHeadType: getArrow(item.false.type),
          animated: item.false.data.isHighlighted,
          data: { isHighlighted: item.false.data.isHighlighted },
        });
      }
    });

    return edges;
  }
}

const getArrow = (type: RoutingNodeType) =>
  ['rule', 'rule_group', 'risk_profile', 'machine_learning'].includes(type)
    ? ArrowHeadType.ArrowClosed
    : undefined;

export default RoutingGrid;
