import React, { useMemo } from 'react';
import {
  Box,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
} from '@cko/primitives';
import DecisionRow from '../DecisionRow';
import {
  PaymentProcessingStep,
  PaymentProcessingDecisions,
  DynamicDecision,
  UpdateDecisionPayload,
} from 'types/models';

const getPreviousEnd = (decisions: DynamicDecision[], i: number): number => {
  const next = decisions[i - 1];

  if (!next) {
    return 0;
  }

  if (next.end) {
    return next.end + 1;
  } else {
    return getPreviousEnd(decisions, i - 1);
  }
};

interface Props {
  decisions: DynamicDecision[];
  handleUpdateScoringRange: (payload: UpdateDecisionPayload) => void;
  handleEnableDecision: (payload: PaymentProcessingDecisions) => void;
  handleDisableDecision: (payload: PaymentProcessingDecisions) => void;
  paymentProcessingStep: PaymentProcessingStep;
}

const DecisionRangeContainer: React.FunctionComponent<Props> = ({
  decisions,
  handleUpdateScoringRange,
  handleEnableDecision,
  handleDisableDecision,
  paymentProcessingStep,
}) => {
  const enabledCount = useMemo(() => {
    return decisions.reduce((count, decision) => {
      if (decision.end) {
        return count + 1;
      }

      return count;
    }, 0);
  }, [decisions]);

  return (
    <>
      <Box p={3}>
        <h1>Scoring decisions</h1>
      </Box>
      <Box pr="10px" pb={3}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell as="th" width="10%"></TableCell>
              <TableCell as="th" width="40%">
                Decision
              </TableCell>
              <TableCell as="th" width="50%">
                Range
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {decisions.map(({ end, decision }, index) => {
              return (
                <DecisionRow
                  key={decision}
                  decision={decision}
                  start={getPreviousEnd(decisions, index)}
                  end={end}
                  handleUpdateScoringRange={handleUpdateScoringRange}
                  handleEnableDecision={handleEnableDecision}
                  handleDisableDecision={handleDisableDecision}
                  enabledCount={enabledCount}
                  paymentProcessingStep={paymentProcessingStep}
                />
              );
            })}
          </TableBody>
        </Table>
      </Box>
    </>
  );
};

export default React.memo(DecisionRangeContainer);
