import React from 'react';
import RoutingSelectMenuList, {
  ScrollableListItems,
} from './BaseSelectMenu/RoutingSelectMenuList';
import { RoutingSelectMenuProvider } from './ContextProvider/RoutingSelectMenuContextProvider';
import RoutingSelectMenuGroup from './BaseSelectMenu/RoutingSelectMenuGroup';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import {
  useCreateRoutingGroupOptions,
  useRiskProfilesOptions,
  useRulesOptions,
  useAssignOutcomeOptions,
} from '../Hooks/useCreateRoutingGroupOptions';
import { groupsWithIndex, isFirstNode } from '../routingUtils';
import RoutingSelectSubGroup from './BaseSelectMenu/RoutingSelectSubgroup';
import RoutingSelectMenuOptions from './BaseSelectMenu/RoutingSelectMenuOptions';
import RoutingDropdownButtons from './RoutingButtons/RoutingDropdownButtons';
import RoutingSelectMenuDeleteButton from './RoutingButtons/RoutingSelectMenuDeleteButton';
import { useTheme } from 'styled-components';
import { useRoutingTree } from '../RoutingTreeContext';
import { useDeleteRoutingElementModal } from '../RoutingContainer/DeleteRoutingElementContainer';
import { FormMode, MLScoreDecision } from 'types/models';
import { useMLModuleModal } from '../MLModuleFormContainer';

type Props = {
  position: GridPosition;
  mlModelName: string;
  isHighlighted: boolean;
  decisions: MLScoreDecision[];
};

const MlModelSelectMenu: React.FunctionComponent<Props> = ({
  position,
  mlModelName,
  isHighlighted,
  decisions,
}) => {
  const {
    actions: { editNode },
  } = useRoutingTree();

  const createOptions = useCreateRoutingGroupOptions(position, editNode);
  const outcomeOptions = useAssignOutcomeOptions(position, editNode);
  const riskProfilesOptions = useRiskProfilesOptions(position, editNode);
  const rulesOptions = useRulesOptions(position, editNode);
  const theme = useTheme();

  const isFirstNodeInGrid = isFirstNode(position);

  const optionsToUse = !isFirstNodeInGrid
    ? [createOptions, riskProfilesOptions, rulesOptions, outcomeOptions]
    : [createOptions, riskProfilesOptions, rulesOptions];

  const optionsWithIndex = groupsWithIndex(optionsToUse);

  const { openModal } = useDeleteRoutingElementModal();

  const { openModal: openMlModal } = useMLModuleModal();

  const [
    createOptionsWithIndex,
    riskProfilesOptionsWithIndex,
    rulesOptionsWithIndex,
    outcomesWithIndex,
  ] = optionsWithIndex;

  return (
    <RoutingSelectMenuProvider items={optionsWithIndex.flat()}>
      <div>
        <RoutingDropdownButtons
          borderColor={isHighlighted ? theme.colors.focus : undefined}
          text={mlModelName}
          isMLNode
          onLeftButtonClicked={() => {
            openMlModal({
              decisions: decisions,
              formMode: FormMode.Edit,
              onSave: (data) => {
                editNode({
                  position,
                  nodeType: 'machine_learning',
                  data,
                });
              },
            });
          }}
        />
        <RoutingSelectMenuList>
          <ScrollableListItems>
            <RoutingSelectMenuGroup title="Create">
              <RoutingSelectMenuOptions
                showIcon
                options={createOptionsWithIndex}
              />
            </RoutingSelectMenuGroup>
            {!isFirstNodeInGrid && (
              <RoutingSelectMenuGroup title="Change to outcome">
                <RoutingSelectMenuOptions options={outcomesWithIndex} />
              </RoutingSelectMenuGroup>
            )}
            <RoutingSelectMenuGroup title="Change to another element">
              <RoutingSelectSubGroup title=" Your Rules">
                <RoutingSelectMenuOptions options={rulesOptionsWithIndex} />
              </RoutingSelectSubGroup>
              <RoutingSelectSubGroup title="Your risk profiles">
                <RoutingSelectMenuOptions
                  options={riskProfilesOptionsWithIndex}
                />
              </RoutingSelectSubGroup>
            </RoutingSelectMenuGroup>
          </ScrollableListItems>
          {!isFirstNodeInGrid && (
            <RoutingSelectMenuDeleteButton
              onDelete={() =>
                openModal({
                  position,
                  nodeType: 'machine_learning',
                })
              }
            />
          )}
        </RoutingSelectMenuList>
      </div>
    </RoutingSelectMenuProvider>
  );
};
export default MlModelSelectMenu;
