import React from 'react';
import styled from 'styled-components';
import { useRoutingSelectMenuContext } from '../ContextProvider/RoutingSelectMenuContextProvider';
import { Portal, Box } from '@cko/primitives';

const StyledListContainer = styled.div(
  ({ theme }) => `
    position: absolute;
    box-shadow: ${theme.shadows.focusLight[2]};
    border-radius: ${theme.radii[1]};
    width: 240px;
    background: white;
`
);

export const ScrollableListItems = styled.div`
  overflow-y: scroll;
  max-height: 350px;
`;

interface Props {
  children: React.ReactNode;
}

const MenuPortal: React.FC = ({ children }) => {
  const { popper } = useRoutingSelectMenuContext();

  return (
    <Portal>
      <div
        ref={popper.setPopperElement}
        style={{ ...popper.styles.popper, zIndex: 9999 }}
        {...popper.attributes.popper}
      >
        {children}
      </div>
    </Portal>
  );
};

const RoutingSelectMenuList: React.FunctionComponent<Props> = ({
  children,
}) => {
  const { getMenuProps, isOpen } = useRoutingSelectMenuContext();

  const Wrapper = isOpen ? MenuPortal : Box;

  return (
    <Wrapper>
      <ul {...getMenuProps()}>
        <StyledListContainer>{isOpen ? children : null}</StyledListContainer>
      </ul>
    </Wrapper>
  );
};

export default RoutingSelectMenuList;
