import React from 'react';
import RoutingSelectMenuList, {
  ScrollableListItems,
} from './BaseSelectMenu/RoutingSelectMenuList';
import { RoutingSelectMenuProvider } from './ContextProvider/RoutingSelectMenuContextProvider';
import RoutingSelectMenuGroup from './BaseSelectMenu/RoutingSelectMenuGroup';
import RoutingDropdownMenuButton from '../RoutingSelectMenu/RoutingButtons/RoutingDropdownMenuButton';
import RoutingSelectMenuOptions from './BaseSelectMenu/RoutingSelectMenuOptions';
import {
  useCreateRoutingGroupOptions,
  useAssignOutcomeOptions,
  useRiskProfilesOptions,
  useRulesOptions,
  useCreateMLModelOptions,
} from '../Hooks/useCreateRoutingGroupOptions';
import MlModelSelectMenuGroup from './MLModelSelectMenuGroup';
import { groupsWithIndex } from '../routingUtils';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import RoutingSelectSubGroup from './BaseSelectMenu/RoutingSelectSubgroup';
import { useRoutingTree } from '../RoutingTreeContext';
import useMerchants from 'hooks/useMerchants';
import { useFeatureFlags } from 'contexts/FeatureFlagsContext';

export type RoutingListItemWithIndex = RoutingListItem & {
  index: number;
};

export type RoutingListItem = {
  label: string;
  action: () => void;
};

type Props = {
  position: GridPosition;
};

const NextElementSelectMenu: React.FunctionComponent<Props> = ({
  position,
}) => {
  const {
    actions: { addNode },
  } = useRoutingTree();
  const { featureFlags } = useFeatureFlags();

  const createOptions = useCreateRoutingGroupOptions(position, addNode);
  const outcomeOptions = useAssignOutcomeOptions(position, addNode);
  const riskProfilesOptions = useRiskProfilesOptions(position, addNode);
  const rulesOptions = useRulesOptions(position, addNode);
  const createMLModelOptions = useCreateMLModelOptions(position, addNode);
  const [{ selectedMerchant }] = useMerchants();

  const [
    createOptionsWithIndex,
    outcomesWithIndex,
    rulesOptionsWithIndex,
    riskProfilesOptionsWithIndex,
    mLModelOptionsWithIndex,
  ] = groupsWithIndex([
    createOptions,
    outcomeOptions,
    rulesOptions,
    riskProfilesOptions,
    createMLModelOptions,
  ]);

  return (
    <RoutingSelectMenuProvider
      items={[
        ...createOptionsWithIndex,
        ...outcomesWithIndex,
        ...rulesOptionsWithIndex,
        ...riskProfilesOptionsWithIndex,
        ...mLModelOptionsWithIndex,
      ]}
    >
      <div>
        <RoutingDropdownMenuButton buttonText="Add element" />
        <RoutingSelectMenuList>
          <ScrollableListItems>
            <RoutingSelectMenuGroup title="Create">
              <RoutingSelectMenuOptions
                showIcon
                options={createOptionsWithIndex}
              />
            </RoutingSelectMenuGroup>
            <RoutingSelectMenuGroup title="Assign outcome">
              <RoutingSelectMenuOptions options={outcomesWithIndex} />
            </RoutingSelectMenuGroup>
            <RoutingSelectMenuGroup title="Use Existing">
              <RoutingSelectSubGroup title=" Your Rules">
                <RoutingSelectMenuOptions options={rulesOptionsWithIndex} />
              </RoutingSelectSubGroup>
              <RoutingSelectSubGroup title=" Your risk profiles">
                <RoutingSelectMenuOptions
                  options={riskProfilesOptionsWithIndex}
                />
              </RoutingSelectSubGroup>
            </RoutingSelectMenuGroup>
            {featureFlags.ROUTING_ML_MODULE &&
              selectedMerchant?.ml_score_enabled && (
                <MlModelSelectMenuGroup options={mLModelOptionsWithIndex} />
              )}
          </ScrollableListItems>
        </RoutingSelectMenuList>
      </div>
    </RoutingSelectMenuProvider>
  );
};

export default NextElementSelectMenu;
