import React from 'react';
import { Modals } from 'types/enums/modals';
import { ParagraphText, Strong } from 'components/Text';
import Section from 'components/Section';
import { TextHeadingThree, ListItem } from '@cko/primitives';
import InfoModal from 'components/InfoModal';
import UnorderedList from 'styles/UnorderedList';

const RoutingInfoBinaryModalContainer = () => (
  <InfoModal modalKey={Modals.ROUTING_INFO_BINARY} title={`Routing`}>
    <article>
      <Section>
        <ParagraphText>
          Routing allows building a decision tree to segment all incoming
          transactions based on the wide set of parameters. At each node of a
          tree is a rule that can either be true or false. Rules can be as
          complex as required and the routing tree can be many levels deep and
          have as many routing branches as deemed practical. You can create
          various rules in the Rules section or from the Routing screen
          directly.
        </ParagraphText>
      </Section>
      <Section>
        <TextHeadingThree mb={3}>Outcomes and Risk Profiles </TextHeadingThree>
        <ParagraphText>
          Each branch must have an Outcome or a Risk profile at the end. This
          way once it is determined that a transaction falls into a particular
          segment Prism will know what to do with it.
          <Strong>Outcomes</Strong> are a fast and easy way to process
          transactions that routing forwards to a particular segment. They are
          best used at the points of the routing where the transaction
          segmentation is very precise.
          <Strong>Risk Profiles</Strong> are a mechanism for scoring-based
          decision-making. They allows weighting multiple criteria
          simultaneously rather than one-by-one and having each of these
          criteria contribute to the final risk score. Risk profiles are best
          used for situations where very precise segmentation is difficult or
          impractical and so a segment can contain transactions of various risk
          levels.
        </ParagraphText>
      </Section>
      <Section>
        <TextHeadingThree mb={3}>Types of routing</TextHeadingThree>
        <ParagraphText>
          here are three main steps in fraud scanning process that correspond to
          the phases of transaction processing:
        </ParagraphText>
        <ParagraphText>
          <UnorderedList pl={3}>
            <ListItem>
              <Strong>Pre3DS</Strong>
            </ListItem>
            <ListItem>
              <Strong>PreAUTH</Strong> (coming soon)
            </ListItem>
            <ListItem>
              <Strong>PostAUTH</Strong>
            </ListItem>
          </UnorderedList>
        </ParagraphText>
        <ParagraphText>
          These steps are mainly characterised by the type of information Prism
          has and types of the decisions it can make. Consequently Prism
          maintains a separate routing for each step.
        </ParagraphText>
      </Section>
    </article>
  </InfoModal>
);

export default RoutingInfoBinaryModalContainer;
