import React from 'react';
import RiskProfilesScoringRulesContainer from 'features/RiskProfiles/RiskProfilesScoringRulesContainer';
import DecisionRangeContainer from 'components/DecisionRanges/DecisionRangeContainer';
import useRiskProfileState from 'features/RiskProfiles/useRiskProfileState';
import {
  PaymentProcessingStep,
  RiskProfileResponse,
  ScoringRuleResponse,
} from 'types/models';
import SpacedButtonGroup from 'components/SpacedButtonGroup';
import { PrimaryButton, OutlineButton, Modal } from '@cko/primitives';
import InlineAlert from 'components/InlineAlert';
import { useUpdateRiskProfile } from 'hooks/api/riskProfiles';
import useAlerts from 'hooks/useAlerts';

export type RiskProfileMetaData = {
  risk_profile_name: string;
  risk_profile_id: string;
};

type Props = {
  step: PaymentProcessingStep;
  onSuccess: (data: RiskProfileMetaData) => void;
  onClose: () => void;
  riskProfileData: RiskProfileResponse;
  rulesData: ScoringRuleResponse;
};

const RiskProfileFormModal: React.FC<Props> = ({
  step,
  onClose,
  onSuccess,
  riskProfileData,
  rulesData,
}) => {
  const { state, actions } = useRiskProfileState(riskProfileData);

  const [updateRiskProfile, { isLoading, error }] = useUpdateRiskProfile();
  const { showAlert } = useAlerts();

  const handleSave = async () => {
    try {
      const payload = {
        score_decisions: state.decisions.map((decision) => ({
          decision: decision.decision,
          end: decision.end ? decision.end : null,
        })),
        scored_rules: state.scored_rules,
      };

      await updateRiskProfile({
        data: payload,
        path: `${step}/${riskProfileData.id}/config`,
      });
      showAlert(`Successfully saved risk profile`, {
        type: 'success',
      });
      actions.handleSave();
      onSuccess({
        risk_profile_name: state.name,
        risk_profile_id: riskProfileData.id,
      });
    } catch {
      showAlert(`Error saving risk profile`, {
        type: 'alert',
      });
    }
  };

  return (
    <Modal size={'large'} closeModal={onClose} title={state.name}>
      <RiskProfilesScoringRulesContainer
        scoredRules={state.scored_rules}
        handleScoredRuleUpdate={actions.handleScoredRuleChange}
        addScoringRule={actions.handleAddScoredRule}
        handleDeleteRiskProfileRule={actions.handleDeleteRiskProfileRule}
        rules={rulesData?.summaries || []}
        riskProfileName={state.name}
      />
      <DecisionRangeContainer
        decisions={state.decisions}
        handleUpdateScoringRange={actions.handleDecisionChange}
        handleEnableDecision={actions.handleEnableDecision}
        handleDisableDecision={actions.handleDisableDecision}
        paymentProcessingStep={step as PaymentProcessingStep}
      />
      <SpacedButtonGroup>
        <PrimaryButton
          variant="large"
          type="submit"
          disabled={isLoading || !state.hasEdited}
          data-cy="submit"
          onClick={handleSave}
        >
          {isLoading ? 'Saving' : 'Save'}
        </PrimaryButton>
        <OutlineButton variant="large" onClick={onClose}>
          Cancel
        </OutlineButton>
      </SpacedButtonGroup>
      {error && <InlineAlert>{error}</InlineAlert>}
    </Modal>
  );
};

export default RiskProfileFormModal;
