import { useSelect, UseSelectReturnValue } from 'downshift';
import React, { createContext, useContext, useMemo } from 'react';
import { usePopper } from 'react-popper';
import {
  RoutingListItem,
  RoutingListItemWithIndex,
} from '../NextElementSelectMenu';
import { useRoutingTree } from '../../RoutingTreeContext';
import { VirtualElement } from '@popperjs/core';

type Props = {
  items: RoutingListItemWithIndex[];
};

type PopperProps = {
  styles: Record<string, React.CSSProperties>;
  attributes: Record<string, Record<string, string>>;
  popperElement?: HTMLElement | null;
  setReferenceElement: React.Dispatch<
    React.SetStateAction<Element | VirtualElement | null>
  >;
  setPopperElement: React.Dispatch<React.SetStateAction<HTMLElement | null>>;
};

export const RoutingSelectMenuContext = createContext<
  (UseSelectReturnValue<RoutingListItem> & { popper: PopperProps }) | undefined
>(undefined);

export const RoutingSelectMenuProvider: React.FunctionComponent<Props> = ({
  items,
  children,
}) => {
  const [referenceElement, setReferenceElement] = React.useState<
    Element | VirtualElement | null
  >(null);
  const [popperElement, setPopperElement] = React.useState<HTMLElement | null>(
    null
  );
  const { styles, attributes } = usePopper(referenceElement, popperElement, {
    placement: 'bottom-start',
  });
  const { setIsDropdownOpen } = useRoutingTree();

  const downshiftUseSelectProps = useSelect<RoutingListItem>({
    items,
    onSelectedItemChange: (change) => {
      if (change.selectedItem) {
        change.selectedItem.action();
      }
    },
    onIsOpenChange: ({ isOpen }) => {
      setIsDropdownOpen(isOpen ?? false);
    },
  });

  const value = useMemo(() => {
    return {
      ...downshiftUseSelectProps,
      popper: {
        setReferenceElement,
        setPopperElement,
        styles,
        attributes,
        popperElement,
      },
    };
  }, [attributes, downshiftUseSelectProps, popperElement, styles]);

  return (
    <RoutingSelectMenuContext.Provider value={value}>
      {children}
    </RoutingSelectMenuContext.Provider>
  );
};

export const useRoutingSelectMenuContext = () => {
  const context = useContext(RoutingSelectMenuContext);
  if (context === undefined) {
    throw new Error(
      'useRoutingSelectMenuContext must be used within a RoutingSelectMenuProvider'
    );
  }

  return context;
};
