import React from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import RoutingDeleteElementModal from '../RoutingSelectMenu/Modal/RoutingDeleteElementModal';
import { useRoutingTree } from '../RoutingTreeContext';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import { RoutingNodeType } from 'types/models';

export const useDeleteRoutingElementModal = () => {
  return useModalManager<{ position: GridPosition; nodeType: RoutingNodeType }>(
    Modals.DELETE_ROUTING_TREE_ELEMENT
  );
};

const DeleteRoutingElementContainer: React.FunctionComponent = () => {
  const { modalData, closeModal } = useDeleteRoutingElementModal();

  const {
    actions: { deleteNode },
  } = useRoutingTree();

  if (!modalData) {
    return null;
  }

  const onDelete = () => {
    deleteNode({ position: modalData.position });
    closeModal();
  };
  return (
    <RoutingDeleteElementModal
      nodeType={modalData.nodeType}
      handleClose={closeModal}
      onDelete={onDelete}
    />
  );
};

export default DeleteRoutingElementContainer;
