import React from 'react';
import { PrimaryButton } from '@cko/primitives';
import styled from 'styled-components';
import { IconActionAdd } from '@cko/icons';
import { useRoutingSelectMenuContext } from '../ContextProvider/RoutingSelectMenuContextProvider';
import usePermissions from 'hooks/usePermissions';
import { mergeRefs } from 'utils/mergeRefs';

interface Props {
  buttonText?: string;
}

const RoutingButton = styled(PrimaryButton)`
  width: 100%;
  font-size: 12px;
  text-align: start;
  padding: 0 0 0 6px;
  height: 32px;
  font-weight: 500;
  svg {
    margin-right: 4px;
  }
`;

const DropdownMenuButton: React.FunctionComponent<Props> = ({
  buttonText = 'Add your first element',
}) => {
  const { getToggleButtonProps, popper } = useRoutingSelectMenuContext();
  const { hasPermission } = usePermissions();
  const { ref, ...buttonProps } = getToggleButtonProps();
  const hasWritePermissions = hasPermission('prism:routing:write');

  return (
    <RoutingButton
      {...buttonProps}
      disabled={!hasWritePermissions}
      ref={mergeRefs([popper.setReferenceElement, ref])}
    >
      <IconActionAdd />
      {buttonText}
    </RoutingButton>
  );
};

export default DropdownMenuButton;
