import { useMemo, useReducer } from 'react';
import RoutingGrid from '../RoutingGrid';
import createRoutingReducer, {
  RoutingTreeState,
  RoutingDataLoadedPayload,
} from './routingTreeReducer';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import { RoutingNodeType } from 'types/models';

export type UpdateRoutingGridPayload<T> = {
  position: GridPosition;
  nodeType: RoutingNodeType;
  data?: T;
};

export type DeleteRoutingGridPayload<T> = {
  position: GridPosition;
  data?: T;
};

export type RoutingActions = {
  addNode: <T>(payload: UpdateRoutingGridPayload<T>) => void;
  editNode: <T>(payload: UpdateRoutingGridPayload<T>) => void;
  dataLoaded: (payload: RoutingDataLoadedPayload) => void;
  highlightPath: (payload: {
    position: GridPosition;
    isHighlighted: boolean;
  }) => void;
  deleteNode: <T>(params: DeleteRoutingGridPayload<T>) => void;
  saveTree: () => void;
};

const useRoutingTreeReducer = (
  routingGrid: RoutingGrid,
  onRoutingChange: (hasStateChange: boolean) => void
): [RoutingTreeState, RoutingActions] => {
  const reducer = useMemo(() => {
    return createRoutingReducer(routingGrid);
  }, [routingGrid]);

  const [state, dispatch] = useReducer(reducer, {
    nodes: routingGrid.createNodes(),
    edges: routingGrid.createEdges(),
    isHovering: false,
    unsavedChanges: false,
  });

  return [
    state,
    {
      addNode: (payload) => {
        dispatch({ type: 'ADD_NODE', payload });
        onRoutingChange(true);
      },
      editNode: (payload) => {
        dispatch({ type: 'EDIT_NODE', payload });
        onRoutingChange(true);
      },
      dataLoaded: (payload: RoutingDataLoadedPayload) => {
        dispatch({
          type: 'DATA_LOADED',
          payload,
        });
      },
      highlightPath: (payload) => {
        dispatch({
          type: 'HIGHLIGHT_PATH',
          payload,
        });
      },
      deleteNode: (payload) => {
        dispatch({ type: 'DELETE_NODE', payload });
        onRoutingChange(true);
      },
      saveTree: () => {
        dispatch({ type: 'SAVE_TREE' });
        onRoutingChange(false);
      },
    },
  ];
};

export default useRoutingTreeReducer;
