import { GridPosition } from '../RoutingGrid/RoutingGrid';
import {
  RoutingNodeType,
  RoutingNode,
  TerminatingRoutingNode,
} from 'types/models';

export const groupsWithIndex = <T>(options: T[][]) => {
  let startingIndex = 0;
  return options.map((group) => {
    return group.map((item) => ({
      ...item,
      index: startingIndex++,
    }));
  });
};

export const isFirstNode = ({ row, col }: GridPosition) => {
  return row === 0 && col === 0;
};

export const isTerminatingNode = (nodeType: RoutingNodeType) => {
  return ['outcome', 'risk_profile', 'machine_learning'].includes(nodeType);
};

export const isTerminatingNodeGuard = (
  node: RoutingNode
): node is TerminatingRoutingNode => {
  return isTerminatingNode(node.type);
};
