import RoutingGrid from '../RoutingGrid';
import { Edge, Node } from 'react-flow-renderer';
import RoutingGridItem from '../RoutingGrid/RoutingGridItem';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import { RoutingNodeType } from 'types/models';
import { isFirstNode, isTerminatingNode } from '../routingUtils';

type ActionPayload<T = any> = {
  data?: T;
};

export type RoutingTreeState = {
  nodes: Node[];
  edges: Edge[];
  isHovering: boolean;
  unsavedChanges: boolean;
};

export type RoutingDataLoadedPayload = ActionPayload & {
  nodes: Node[];
  edges: Edge[];
};

type Action<Type, Payload extends ActionPayload> = {
  type: Type;
  payload: Payload;
};

export type RoutingTreeDataLoadedAction = Action<
  'DATA_LOADED',
  RoutingDataLoadedPayload
>;

type HighlightPathAction = Action<
  'HIGHLIGHT_PATH',
  ActionPayload & {
    position: GridPosition;
    isHighlighted: boolean;
  }
>;

type UpdateGridPayload<T> = ActionPayload<T> & {
  position: GridPosition;
  nodeType: RoutingNodeType;
};

type DeleteNodePayload<T> = ActionPayload<T> & {
  position: GridPosition;
};

type DeleteNodeAction<T> = Action<'DELETE_NODE', DeleteNodePayload<T>>;
type AddNodeAction<T> = Action<'ADD_NODE', UpdateGridPayload<T>>;

type EditNodeAction<T> = Action<'EDIT_NODE', UpdateGridPayload<T>>;

type SaveRoutingTreeAction = {
  type: 'SAVE_TREE';
};

export type RoutingTreeActions<T> =
  | AddNodeAction<T>
  | RoutingTreeDataLoadedAction
  | HighlightPathAction
  | DeleteNodeAction<T>
  | EditNodeAction<T>
  | SaveRoutingTreeAction;

const getRootNode = (node: RoutingGridItem) => {
  let result = node;
  while (result.parent !== null) {
    result = result.parent;
  }

  return result;
};

const addTrueFalseNodesToGrid = ({
  position,
  currentNode,
  routingGrid,
}: {
  position: GridPosition;
  currentNode: RoutingGridItem;
  routingGrid: RoutingGrid;
}) => {
  const trueNode = new RoutingGridItem('addNextElement', {});
  const falseNode = new RoutingGridItem('addNextElement', {});
  currentNode.true = trueNode;
  currentNode.false = falseNode;

  trueNode.parent = currentNode;
  falseNode.parent = currentNode;

  routingGrid.fromTree(getRootNode(currentNode));
};

const createRoutingReducer = <T>(routingGrid: RoutingGrid) => {
  return (state: RoutingTreeState, action: RoutingTreeActions<T>) => {
    switch (action.type) {
      case 'DATA_LOADED':
        const { nodes, edges } = action.payload;
        return {
          ...state,
          nodes,
          edges,
          unsavedChanges: false,
        };

      case 'ADD_NODE': {
        const { position } = action.payload;
        const currentNode = routingGrid.getItem(position);

        if (!currentNode) {
          return state;
        }

        currentNode.type = action.payload.nodeType;
        currentNode.data = action.payload.data ?? {};

        if (
          action.payload.nodeType === 'rule' ||
          action.payload.nodeType === 'rule_group'
        ) {
          if (isFirstNode(position)) {
            const falseNode = new RoutingGridItem('addNextElement', {});
            currentNode.false = falseNode;
            falseNode.parent = currentNode;

            routingGrid.fromTree(falseNode);
          } else {
            addTrueFalseNodesToGrid({ currentNode, routingGrid, position });
          }
        } else if (
          isFirstNode(position) &&
          (action.payload.nodeType === 'risk_profile' ||
            action.payload.nodeType === 'machine_learning')
        ) {
          currentNode.true = null;
          routingGrid.fromTree(getRootNode(currentNode));
        }

        return {
          ...state,
          isHovering: false,
          nodes: routingGrid.createNodes(),
          edges: routingGrid.createEdges(),
          unsavedChanges: true,
        };
      }

      case 'EDIT_NODE': {
        const { position, nodeType, data } = action.payload;
        const currentNode = routingGrid.getItem(position);

        if (!currentNode) {
          return state;
        }

        if (
          isTerminatingNode(currentNode.type) &&
          !isTerminatingNode(nodeType)
        ) {
          addTrueFalseNodesToGrid({ currentNode, routingGrid, position });
        } else if (isTerminatingNode(nodeType)) {
          currentNode.visitChildBranches((item) => {
            item.parent = null;
          });
          currentNode.true = null;
          currentNode.false = null;

          routingGrid.fromTree(getRootNode(currentNode));
        }

        currentNode.type = nodeType;
        currentNode.data = data ?? {};

        highlight(currentNode, false);

        return {
          ...state,
          isHovering: false,
          nodes: routingGrid.createNodes(),
          edges: routingGrid.createEdges(),
          unsavedChanges: true,
        };
      }

      case 'HIGHLIGHT_PATH': {
        const { position, isHighlighted } = action.payload;
        const currentNode = routingGrid.getItem(position);

        if (currentNode) {
          highlight(currentNode, isHighlighted);
        }

        return {
          ...state,
          isHovering: isHighlighted,
          nodes: routingGrid.createNodes(),
          edges: routingGrid.createEdges(),
        };
      }

      case 'DELETE_NODE': {
        const { position } = action.payload;
        const currentNode = routingGrid.getItem(position);

        if (!currentNode) {
          return state;
        }

        const newRoutingGridItem = new RoutingGridItem('addNextElement', {});
        const parentNode = currentNode.parent;

        if (parentNode) {
          newRoutingGridItem.parent = parentNode;

          if (parentNode.false?.id === currentNode.id) {
            parentNode.false = newRoutingGridItem;
          }

          if (parentNode.true?.id === currentNode.id) {
            parentNode.true = newRoutingGridItem;
          }
        }

        routingGrid.fromTree(getRootNode(newRoutingGridItem));

        return {
          ...state,
          nodes: routingGrid.createNodes(),
          edges: routingGrid.createEdges(),
          unsavedChanges: true,
        };
      }

      case 'SAVE_TREE':
        return {
          ...state,
          unsavedChanges: false,
        };

      default:
        return state;
    }
  };
};

const highlight = (node: RoutingGridItem, isHighlighted: boolean) => {
  node.data.isHighlighted = isHighlighted;
  if (node.parent) {
    highlight(node.parent, isHighlighted);
  }
};

export default createRoutingReducer;
