import React, { useCallback, useEffect } from 'react';
import { Prompt, useLocation } from 'react-router-dom';
import { Prompts } from 'types/models';
import * as H from 'history';

interface Props {
  when: boolean;
  promptKey: Prompts | ((location: H.Location) => Prompts | boolean);
  message?: string;
}

const SavePrompt: React.FC<Props> = ({
  when,
  promptKey,
  message = 'message',
}) => {
  const { pathname } = useLocation();
  const handleBeforeunload = useCallback(
    (evt: BeforeUnloadEvent) => {
      if (when) {
        evt.preventDefault();
        evt.returnValue = message;
      }

      return when ? message : '';
    },
    [message, when]
  );

  useEffect(() => {
    window.addEventListener('beforeunload', handleBeforeunload);
    return () => window.removeEventListener('beforeunload', handleBeforeunload);
  }, [handleBeforeunload]);

  return (
    <Prompt
      when={when}
      message={(params) => {
        if (params.pathname !== pathname) {
          if (typeof promptKey === 'function') {
            return promptKey(params);
          }
          return promptKey;
        }
        return false;
      }}
    />
  );
};

export default SavePrompt;
