import { DynamicDecision } from 'types/models';

class ScoringDecisionNode {
  public value: DynamicDecision;
  public next: ScoringDecisionNode | null;
  public prev: ScoringDecisionNode | null;
  public nextEnd: ScoringDecisionNode | null;
  public prevEnd: ScoringDecisionNode | null;

  constructor(value: DynamicDecision) {
    this.value = value;
    this.next = null;
    this.prev = null;
    this.nextEnd = null;
    this.prevEnd = null;
  }

  findPrevEndNode(node = this.prev): ScoringDecisionNode | null {
    if (node === null) {
      return null;
    }

    if (node.value.end) {
      return node;
    }

    return this.findPrevEndNode(node.prev as this);
  }

  findNextEndNode(node = this.next): ScoringDecisionNode | null {
    if (node === null) {
      return null;
    }

    if (node.value.end) {
      return node;
    }

    return this.findNextEndNode(node.next as this);
  }

  pushOverlappingNextNodes(currentNode = this, nextNode = this.nextEnd): void {
    if (!currentNode || !nextNode) {
      return;
    }

    if (nextNode.nextEnd && nextNode.value.end - 2 <= currentNode.value.end) {
      nextNode.value.end = Math.min(nextNode.value.end + 2, 98);
    }

    return this.pushOverlappingNextNodes(nextNode as this, nextNode.nextEnd);
  }

  fixPreviousOverlappingNodes(
    currentNode = this,
    prevNode = this.prevEnd
  ): void {
    if (!currentNode || !prevNode) {
      return;
    }

    const differenceInRange = currentNode.value.end - prevNode.value.end;

    if (currentNode.prev && differenceInRange < 2) {
      prevNode.value.end = prevNode.value.end - 2;
    }

    return this.fixPreviousOverlappingNodes(prevNode as this, prevNode.prevEnd);
  }

  previousNodesTouching() {
    let prevNode = this.prevEnd;
    let currentEnd = this.value.end;

    while (prevNode) {
      const differenceInRange = currentEnd - prevNode.value.end;

      if (
        (!prevNode.prevEnd && prevNode.value.end > 2) ||
        differenceInRange > 2
      ) {
        return false;
      }

      currentEnd = prevNode.value.end;
      prevNode = prevNode.prevEnd;
    }

    return true;
  }

  touchingPreviousNode() {
    if (this.prevEnd === null) {
      return false;
    }

    const rangeDifference = this.value.end - this.prevEnd.value.end;

    return rangeDifference <= 2;
  }
}

export default ScoringDecisionNode;
