import React from 'react';
import { Modal } from '@cko/primitives';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import AddRiskProfilesScoringRuleForm from 'forms/AddRiskProfileScoringRuleForm';
import { ScoringRuleSummary } from 'types/models';
import { AddScoredRuleActionPayload } from './useRiskProfileState/riskProfileReducer';

interface Props {
  addScoringRule: (payload: AddScoredRuleActionPayload) => void;
  riskProfileName: string;
}

const AddRiskProfilesScoringRuleContainer: React.FunctionComponent<Props> = ({
  addScoringRule,
  riskProfileName,
}) => {
  const { modalData, closeModal } = useModalManager<ScoringRuleSummary[]>(
    Modals.ADD_RISK_PROFILE_SCORING_RULE
  );

  const handleSubmit = (payload: AddScoredRuleActionPayload) => {
    addScoringRule(payload);
    closeModal();
  };

  if (!modalData) {
    return null;
  }

  return (
    <Modal
      type="success"
      title={`Assign rule to ${riskProfileName}`}
      closeModal={closeModal}
    >
      <AddRiskProfilesScoringRuleForm
        onCancel={closeModal}
        onSubmit={handleSubmit}
        scoringRules={modalData}
      />
    </Modal>
  );
};

export default AddRiskProfilesScoringRuleContainer;
