import React, { useCallback } from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import { Box, Flex, Modal } from '@cko/primitives';
import { PaymentProcessingStep, FormMode } from 'types/models';
import RiskProfileFormModal, {
  RiskProfileMetaData,
} from './RiskProfileFormModal';
import { useRiskProfileData } from 'hooks/api/riskProfiles';
import Loader from 'components/Loader';
import { useRulesData } from 'hooks/api/rules';
import endpoints from 'api/endpoints';
import { queryCache } from 'react-query';

export const useRoutingRiskProfileModal = () => {
  return useModalManager<{
    formMode: FormMode;
    id: string;
    onRequestSuccess?: (data: RiskProfileMetaData) => void;
  }>(Modals.ROUTING_RISK_PROFILE_FORM);
};

const RiskProfileFormContainer: React.FC<{
  step: PaymentProcessingStep;
}> = ({ step }) => {
  const { closeModal, modalData } = useRoutingRiskProfileModal();

  const {
    data: riskProfileData,
    status: riskProfileDataStatus,
  } = useRiskProfileData(step as PaymentProcessingStep, modalData?.id);

  const { data: rulesData, status: rulesDataStatus } = useRulesData();

  const loadingRiskProfileData = riskProfileDataStatus === 'loading';
  const loadingRulesData = rulesDataStatus === 'loading';

  const loading = loadingRiskProfileData || loadingRulesData;

  const handleClose = useCallback(() => {
    queryCache.invalidateQueries([endpoints.riskProfiles]);
    closeModal();
  }, [closeModal]);

  if (!modalData) {
    return null;
  }

  if (loading) {
    return (
      <Modal size="large" closeModal={() => {}}>
        <Box height="250px">
          <Flex justifyContent="center" height="100%">
            <Loader size="large" />
          </Flex>
        </Box>
      </Modal>
    );
  }

  if (!riskProfileData || !rulesData) {
    return null;
  }

  return (
    <RiskProfileFormModal
      onSuccess={(data) => {
        modalData.onRequestSuccess && modalData.onRequestSuccess(data);
        handleClose();
      }}
      onClose={handleClose}
      step={step}
      riskProfileData={riskProfileData}
      rulesData={rulesData}
    />
  );
};

export default RiskProfileFormContainer;
