import ScoringDecisionNode from './ScoringDecisionNode';
import { DynamicDecision, PaymentProcessingDecisions } from 'types/models';

class ScoringDecisionList {
  public head: ScoringDecisionNode | null;
  public endHead: ScoringDecisionNode | null;
  public endTail: ScoringDecisionNode | null;
  public tail: ScoringDecisionNode | null;

  constructor() {
    this.head = null;
    this.tail = null;
    this.endHead = null;
    this.endTail = null;
  }

  add(value: DynamicDecision) {
    const hasEnd = value.end !== undefined;
    const node = new ScoringDecisionNode(value);

    // handle first item
    if (!this.head) {
      this.head = node;
    }

    // handle first item with an end
    if (!this.endHead && hasEnd) {
      this.endHead = node;
    }

    if (this.tail) {
      this.tail.next = node;
      node.prev = this.tail;

      if (hasEnd) {
        const prevEnd = node.findPrevEndNode();

        if (prevEnd) {
          prevEnd.nextEnd = node;
        }

        node.prevEnd = prevEnd;
        this.tail.nextEnd = node;
      }
    }

    if (hasEnd) {
      this.endTail = node;
    }

    this.tail = node;
  }

  findByDecision(decision: PaymentProcessingDecisions) {
    let currentItem = this.head;

    while (currentItem) {
      if (currentItem.value.decision === decision) {
        return currentItem;
      }

      currentItem = currentItem.next;
    }

    return null;
  }

  toArray() {
    let arr = [];
    let currentItem = this.head;

    while (currentItem) {
      arr.push(currentItem.value);
      currentItem = currentItem.next;
    }

    return arr;
  }

  toEndArray() {
    let arr = [];
    let currentItem = this.endHead;

    while (currentItem) {
      arr.push(currentItem.value);
      currentItem = currentItem.nextEnd;
    }

    return arr;
  }
}

export default ScoringDecisionList;
