import { RoutingListItem } from '../RoutingSelectMenu/NextElementSelectMenu';
import { useRoutingTree } from '../RoutingTreeContext';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import {
  FormMode,
  Pre3dsDecisions,
  PaymentProcessingStep,
  PostAuthDecisions,
  MLScoreDecision,
  PaymentProcessingDecisions,
} from 'types/models';
import { getDecisionMap, threeDS1DecisionFilter } from 'utils/decisions';
import { useMemo } from 'react';
import { useRulesBuilderModal } from 'features/RulesBuilder/RulesBuilderContainer/RulesBuilderContainer';
import { UpdateRoutingGridPayload } from '../useRoutingTreeReducer/useRoutingTreeReducer';
import { useThreeDSVersion } from 'contexts/ThreeDSVersionContext';
import { useCreateRoutingRiskProfileModal } from '../CreateRiskProfileFormContainer';
import { useMLModuleModal } from '../MLModuleFormContainer';

export const useCreateRoutingGroupOptions = (
  position: GridPosition,
  onSelect: <T>(params: UpdateRoutingGridPayload<T>) => void
): RoutingListItem[] => {
  const { openModal: openRuleModal } = useRulesBuilderModal();

  const {
    openModal: openCreateRiskProfileModal,
  } = useCreateRoutingRiskProfileModal();

  const createOptions = [
    {
      label: 'New rule',
      action: () => {
        openRuleModal({
          mode: FormMode.Add,
          modalId: 'Routing:RulesContainer',
          onRequestSuccess: (data) => {
            onSelect<{ rule_name: string; rule_id: string }>({
              position,
              nodeType: 'rule',
              data: { rule_name: data.name, rule_id: data.id },
            });
          },
        });
      },
    },
    {
      label: 'New risk profile',
      action: () => {
        openCreateRiskProfileModal({
          formMode: FormMode.Add,
          onRequestSuccess: (data) => {
            onSelect({
              position,
              nodeType: 'risk_profile',
              data,
            });
          },
        });
      },
    },
  ];

  return createOptions;
};

export const useAssignOutcomeOptions = (
  position: GridPosition,
  onSelect: <T>(params: UpdateRoutingGridPayload<T>) => void
): RoutingListItem[] => {
  const { step } = useRoutingTree();

  const { version } = useThreeDSVersion();

  const outcomeOptions = useMemo(() => {
    const decisions = Object.entries(
      getDecisionMap(step, version).displayValues
    ).filter(([k]) => threeDS1DecisionFilter(version)(k as Pre3dsDecisions));

    return decisions.map(([key, label]) => {
      return {
        label,
        action: () => {
          const decision = key as PaymentProcessingDecisions;

          return onSelect<{ decision: PaymentProcessingDecisions }>({
            position,
            nodeType: 'outcome',
            data: { decision },
          });
        },
      };
    });
  }, [onSelect, position, step, version]);

  return outcomeOptions;
};

export const useRiskProfilesOptions = (
  position: GridPosition,
  onSelect: <T>(params: UpdateRoutingGridPayload<T>) => void
): RoutingListItem[] => {
  const { riskProfiles } = useRoutingTree();

  const riskProfileOptions = useMemo(() => {
    return riskProfiles.map((riskProfile) => {
      return {
        label: riskProfile.name,
        action: () =>
          onSelect<{ risk_profile_name: string; risk_profile_id: string }>({
            position,
            nodeType: 'risk_profile',
            data: {
              risk_profile_name: riskProfile.name,
              risk_profile_id: riskProfile.id,
            },
          }),
      };
    });
  }, [onSelect, position, riskProfiles]);

  return riskProfileOptions;
};

export const useRulesOptions = (
  position: GridPosition,
  onSelect: (
    params: UpdateRoutingGridPayload<{
      rule_id: string;
      rule_name: string;
    }>
  ) => void
): RoutingListItem[] => {
  const { rules } = useRoutingTree();

  const rulesOptions = useMemo(() => {
    return rules.map((rule) => {
      return {
        label: rule.name,
        action: () =>
          onSelect({
            position,
            nodeType: 'rule',
            data: {
              rule_id: rule.id,
              rule_name: rule.name,
            },
          }),
      };
    });
  }, [onSelect, position, rules]);

  return rulesOptions;
};

export const useCreateMLModelOptions = (
  position: GridPosition,
  onSelect: (
    params: UpdateRoutingGridPayload<{ score_decisions: MLScoreDecision[] }>
  ) => void
): RoutingListItem[] => {
  const { step } = useRoutingTree();

  const initialDecisions = useMemo(
    () =>
      ({
        [PaymentProcessingStep.Pre3ds]: [
          {
            result: {
              type: 'outcome',
              decision: Pre3dsDecisions.TryExemptions,
            },
          },
          {
            result: {
              type: 'outcome',
              decision: Pre3dsDecisions.FrictionlessLiability,
            },
          },
          {
            result: { type: 'outcome', decision: Pre3dsDecisions.NoPreference },
            end: 80,
          },
          { result: { type: 'outcome', decision: Pre3dsDecisions.Challenge } },
          {
            result: { type: 'outcome', decision: Pre3dsDecisions.Decline },
            end: 100,
          },
        ],
        [PaymentProcessingStep.PostAuth]: [
          {
            result: { type: 'outcome', decision: PostAuthDecisions.Capture },
            end: 80,
          },
          { result: { type: 'outcome', decision: PostAuthDecisions.Flag } },
          {
            result: { type: 'outcome', decision: PostAuthDecisions.Void },
            end: 100,
          },
        ],
      }[step] as MLScoreDecision[]),
    [step]
  );

  const { openModal } = useMLModuleModal();
  const createOptions = [
    {
      label: 'Checkout model',
      action: () => {
        openModal({
          formMode: FormMode.Add,
          decisions: initialDecisions,
          onSave: (data) => {
            onSelect({
              position,
              nodeType: 'machine_learning',
              data,
            });
          },
        });
      },
    },
  ];

  return createOptions;
};
