import React from 'react';
import { Handle, Position } from 'react-flow-renderer';
import { PaymentProcessingDecisions, ExtendedNodeProps } from 'types/models';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import { useRoutingNodeColor } from '../Hooks/useRoutingNodeColor';
import { useRoutingTree } from '../RoutingTreeContext';
import OutcomeSelectMenu from '../RoutingSelectMenu/OutcomeSelectMenu';
import RoutingNode from './RoutingNode';

const OutcomeNode: React.FC<
  ExtendedNodeProps<{
    decision: PaymentProcessingDecisions;
    position: GridPosition;
    isHighlighted: boolean | undefined;
  }>
> = ({ data, id }) => {
  const getNodeColor = useRoutingNodeColor();
  const nodeColor = getNodeColor(data.decision);
  const {
    state: { isHovering },
    actions: { highlightPath },
  } = useRoutingTree();

  return (
    <RoutingNode
      onMouseEnter={() => {
        highlightPath({ position: data.position, isHighlighted: true });
      }}
      onMouseLeave={() => {
        highlightPath({ position: data.position, isHighlighted: false });
      }}
      style={{
        opacity: isHovering && !data.isHighlighted ? 0.5 : 1,
      }}
      data-cy={`outcome-node-${id}`}
      data-grid-position={`${data.position.col}:${data.position.row}`}
    >
      <Handle
        type="target"
        position={Position.Left}
        isConnectable={false}
        style={{
          backgroundColor: nodeColor,
          opacity: 1,
          height: '10px',
          width: '10px',
          left: -4,
          border: 'none',
        }}
      />
      <OutcomeSelectMenu
        isHighlighted={data.isHighlighted || false}
        decision={data.decision}
        position={data.position}
      />
    </RoutingNode>
  );
};

export default OutcomeNode;
