import React from 'react';
import { MiniMap } from 'react-flow-renderer';
import { Box, theme } from '@cko/primitives';
import styled from 'styled-components';
import { useRoutingNodeColor } from '../Hooks/useRoutingNodeColor';
import MiniMapControls from './MiniMapControls';

const MiniMapControllersWrapper = styled(Box)`
  & .react-flow__minimap {
    border: 1px solid ${({ theme }) => theme.colors.base};
    border-radius: 3px;
    width: 107px;
    height: 58px;
  }
  & .react-flow__minimap-mask {
    fill: white;
  }
  position: absolute;
  right: 0;
  top: 0;
  border-bottom: 1px solid ${({ theme }) => theme.colors.baseLight};
  border-left: 1px solid ${({ theme }) => theme.colors.baseLight};
  border-radius: 0 0 0 3px;
  height: 88px;
  width: 183px;
  padding: 10px;
  background: white;
  z-index: 5;
`;

const RoutingMiniMap = () => {
  const getNodeColor = useRoutingNodeColor();

  return (
    <MiniMapControllersWrapper>
      <MiniMap
        style={{ top: 15, right: 56 }}
        nodeColor={(node) => {
          switch (node.type) {
            case 'rule':
              return `${theme.colors.baseLight}`;
            case 'add_elements':
              return `${theme.colors.success}`;
            case 'addFirstElement':
              return `${theme.colors.success}`;
            case 'addNextElement':
              return `${theme.colors.success}`;
            case 'outcome':
              return `${getNodeColor(node.data.decision)}`;
            case 'risk_profile':
              return `${theme.colors.baseLight}`;
            case 'rule_group':
              return `${theme.colors.baseLight}`;
            case 'machine_learning':
              return `${theme.colors.baseLight}`;
            default:
              return `${theme.colors.baseLight}`;
          }
        }}
      />
      <MiniMapControls />
    </MiniMapControllersWrapper>
  );
};

export default RoutingMiniMap;
