import React, { useCallback } from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import { PaymentProcessingStep, FormMode, MLScoreDecision } from 'types/models';
import MLModuleFormModal from './MLModuleFormModal';
import useAlerts from 'hooks/useAlerts';

export const useMLModuleModal = () => {
  return useModalManager<{
    formMode: FormMode;
    decisions: MLScoreDecision[];
    onSave: (data: { score_decisions: MLScoreDecision[] }) => void;
  }>(Modals.ROUTING_ML_MODULE_FORM);
};

const MLModuleFormContainer: React.FC<{
  step: PaymentProcessingStep;
}> = ({ step }) => {
  const { closeModal, modalData } = useMLModuleModal();
  const { showAlert } = useAlerts();

  const handleClose = useCallback(() => {
    closeModal();
  }, [closeModal]);

  if (!modalData) {
    return null;
  }

  return (
    <MLModuleFormModal
      onSave={(data) => {
        modalData.onSave(data);
        handleClose();
        showAlert('Machine learning model configuration saved successfully');
      }}
      onClose={handleClose}
      step={step}
      decisions={modalData.decisions}
      mode={modalData.formMode}
    />
  );
};

export default MLModuleFormContainer;
