import React, { useState } from 'react';
import { Form, Formik } from 'formik';
import TextField from 'components/TextField';
import SpacedButtonGroup from 'components/SpacedButtonGroup';
import { PrimaryButton, OutlineButton } from '@cko/primitives';
import createRiskProfileValidator from './CreateRiskProfileValidator';
import InlineAlert from 'components/InlineAlert';

interface Props {
  onSubmit: (data: { data: CreateRiskProfileFormData }) => Promise<any>;
  onCancel: () => void;
  onSuccess?: (data: CreateRiskProfileFormData) => void;
  initialValues: CreateRiskProfileFormData;
  deafultButtonText?: string;
}

export interface CreateRiskProfileFormData {
  name: string;
}

const CreateRiskProfileForm: React.FunctionComponent<Props> = ({
  onSubmit,
  onCancel,
  onSuccess = () => {},
  initialValues,
  deafultButtonText = 'Create',
}) => {
  const [hasSubmissionError, setHasSubmissionError] = useState<string | null>(
    null
  );

  return (
    <Formik
      initialValues={initialValues}
      validate={createRiskProfileValidator}
      validateOnChange={false}
      onSubmit={async (values, actions) => {
        try {
          setHasSubmissionError(null);
          await onSubmit({ data: values });
          onSuccess(values);
        } catch (e) {
          if (e.response?.status === 409) {
            setHasSubmissionError(
              ` A risk profile named "${values.name}" already exists`
            );
          } else {
            setHasSubmissionError('Technical error. Please try again later.');
          }
        } finally {
          actions.setSubmitting(false);
        }
      }}
    >
      {({ isSubmitting, submitCount }) => {
        return (
          <Form>
            <TextField
              name="name"
              label="Risk profile name"
              mb={4}
              data-testid="risk-profile-create-name"
              placeholder="Name of the risk profile"
              submitCount={submitCount}
            />

            <SpacedButtonGroup>
              <PrimaryButton
                variant="large"
                type="submit"
                disabled={isSubmitting}
                data-cy="submit"
              >
                {isSubmitting ? 'Submitting' : deafultButtonText}
              </PrimaryButton>
              <OutlineButton variant="large" onClick={onCancel}>
                Cancel
              </OutlineButton>
            </SpacedButtonGroup>
            {hasSubmissionError && (
              <InlineAlert>{hasSubmissionError}</InlineAlert>
            )}
          </Form>
        );
      }}
    </Formik>
  );
};

export default CreateRiskProfileForm;
