import { useAddUpdateMutation } from 'hooks/api/common/useAddUpdateMutation';
import { RoutingResponse, PaymentProcessingStep } from 'types/models';
import endpoints from 'api/endpoints';
import { MutateConfig } from 'react-query';
import { AxiosError } from 'axios';

export const useUpdateV2Routing = (
  step: PaymentProcessingStep,
  mutationOptions: MutateConfig<
    {},
    AxiosError<{}>,
    { data: RoutingResponse; path?: string }
  > = {}
) => {
  return useAddUpdateMutation<RoutingResponse, {}>(
    'put',
    `${endpoints.routing}/v2/${step}`,
    { ...mutationOptions }
  );
};
