import React from 'react';
import { RoutingListItemWithIndex } from '../NextElementSelectMenu';
import RoutingSelectMenuItem from './RoutingSelectMenuItem';
import RoutingEmptyState from './RoutingEmptyState';
import { Box } from '@cko/primitives';

interface Props {
  options: RoutingListItemWithIndex[];
  showIcon?: boolean;
}

const RoutingSelectMenuOptions: React.FunctionComponent<Props> = ({
  options,
  showIcon,
}) => {
  if (options.length === 0) {
    return (
      <Box pl={3}>
        <RoutingEmptyState />
      </Box>
    );
  }

  return (
    <>
      {options.map((item) => {
        return (
          <RoutingSelectMenuItem
            showIcon={showIcon}
            key={`${item.label}${item.index}`}
            item={item}
            index={item.index}
            title={item.label}
          />
        );
      })}
    </>
  );
};

export default RoutingSelectMenuOptions;
