import React from 'react';
import { TextField } from '@cko/primitives';

import {
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
} from '@cko/primitives';
import { RiskProfileScoredRule } from 'types/models';

import usePermissions from 'hooks/usePermissions';

import DeleteButton from 'components/DeleteButton';

export type HandleScoredRuleUpdate = ({
  index,
  score,
}: {
  index: number;
  score: string;
}) => void;

interface Props {
  scoredRules: RiskProfileScoredRule[];
  handleScoredRuleUpdate: HandleScoredRuleUpdate;
  handleDeleteRiskProfileRule: (rule: RiskProfileScoredRule) => void;
}

const digitsOnly = /^-?\d*\.?\d*$/;

const RiskProfileScoringRulesTable: React.FunctionComponent<Props> = ({
  scoredRules,
  handleScoredRuleUpdate,
  handleDeleteRiskProfileRule,
}) => {
  const { hasPermission } = usePermissions();
  return (
    <Table tableLayout="fixed">
      <TableHead>
        <TableRow>
          <TableCell width="75%" as="th">
            Rule name
          </TableCell>
          <TableCell width="10%" as="th">
            Score
          </TableCell>
          <TableCell width="15%" textAlign="right" as="th" />
        </TableRow>
      </TableHead>
      <TableBody>
        {scoredRules.length > 0 ? (
          scoredRules.map((scoredRule, i) => (
            <TableRow
              key={scoredRule.scoring_rule_id}
              data-cy="risk-profile-scoring-rule"
            >
              <TableCell>{scoredRule.name}</TableCell>
              <TableCell>
                <TextField
                  type="text"
                  data-testid="risk-profile-scoring-rules-table-text-field"
                  disabled={!hasPermission('prism:risk-profile:write')}
                  onChange={(e) => {
                    const { value } = e.target;
                    if (digitsOnly.test(value)) {
                      handleScoredRuleUpdate({
                        index: i,
                        score: value,
                      });
                    }
                  }}
                  onBlur={(e) => {
                    if (e.target.value === '') {
                      handleScoredRuleUpdate({
                        index: i,
                        score: '0',
                      });
                    }
                  }}
                  color={Number(scoredRule.score) > 0 ? 'danger' : 'success'}
                  value={scoredRule.score}
                />
              </TableCell>
              <TableCell textAlign="right">
                <DeleteButton
                  onClick={() => handleDeleteRiskProfileRule(scoredRule)}
                />
              </TableCell>
            </TableRow>
          ))
        ) : (
          <TableRow data-cy="risk-profile-rules-placeholder">
            <TableCell colSpan={3} textAlign="center">
              No rules assigned to this risk profile
            </TableCell>
          </TableRow>
        )}
      </TableBody>
    </Table>
  );
};

export default RiskProfileScoringRulesTable;
