import {
  createValidator,
  required,
  maxLength,
  expectedCharacters,
} from 'utils/validator';
import { CreateRiskProfileFormData } from './CreateRiskProfileForm';

const createRiskProfileValidator = createValidator<CreateRiskProfileFormData>({
  name: [
    required('Name is required'),
    maxLength(101, 'Name must be 100 characters or under'),
    expectedCharacters(
      /^[0-9A-Za-z -]+$/,
      'Name can not contain invalid characters'
    ),
  ],
});

export default createRiskProfileValidator;
