import React, { useState } from 'react';
import DecisionRangeContainer from 'components/DecisionRanges/DecisionRangeContainer';
import {
  DynamicDecision,
  FormMode,
  MLScoreDecision,
  PaymentProcessingStep,
} from 'types/models';
import SpacedButtonGroup from 'components/SpacedButtonGroup';
import { PrimaryButton, OutlineButton } from '@cko/primitives';
import { OpenInfoModalButton } from 'components/InfoModal';
import { Modals } from 'types/enums/modals';
import useDecisionRanges from 'hooks/useDecisionRanges';
import InfoSection from './InfoSection';
import MLModalWithHeader from './MLModalWithHeader';

type Props = {
  step: PaymentProcessingStep;
  onSave: (data: { score_decisions: MLScoreDecision[] }) => void;
  onClose: () => void;
  decisions: MLScoreDecision[];
  mode: FormMode;
};

const transformToDynamicDecision = (
  decisions: MLScoreDecision[]
): DynamicDecision[] => {
  return decisions.map((decision) => ({
    end: decision.end!,
    decision: decision.result.decision,
  }));
};

const transformToMLDecision = (
  decisions: DynamicDecision[]
): MLScoreDecision[] => {
  return decisions.map((decision) => ({
    end: decision.end,
    result: { type: 'outcome', decision: decision.decision },
  }));
};

const MLModuleFormModal: React.FC<Props> = ({
  step,
  onClose,
  onSave,
  decisions,
  mode,
}) => {
  const [hasEdited, setHasEdited] = useState(mode === FormMode.Add);

  const { state, actions } = useDecisionRanges(
    transformToDynamicDecision(decisions),
    setHasEdited
  );

  const handleSave = () => {
    onSave({
      score_decisions: transformToMLDecision(state.decisions),
    });
  };

  return (
    <MLModalWithHeader
      closeModal={onClose}
      title="Checkout machine learning model"
      headerContent={
        <OpenInfoModalButton
          buttonText="View the FAQs"
          modalKey={Modals.ML_INFO_MODAL}
        />
      }
    >
      <InfoSection />
      <DecisionRangeContainer
        decisions={state.decisions}
        handleUpdateScoringRange={actions.handleDecisionChange}
        handleEnableDecision={actions.handleEnableDecision}
        handleDisableDecision={actions.handleDisableDecision}
        paymentProcessingStep={step as PaymentProcessingStep}
      />
      <SpacedButtonGroup>
        <PrimaryButton
          variant="large"
          type="submit"
          disabled={!hasEdited}
          data-cy="submit"
          onClick={handleSave}
        >
          Save
        </PrimaryButton>
        <OutlineButton variant="large" onClick={onClose}>
          Cancel
        </OutlineButton>
      </SpacedButtonGroup>
    </MLModalWithHeader>
  );
};

export default MLModuleFormModal;
