import React, { useMemo } from 'react';
import { Handle, Position } from 'react-flow-renderer';

import { ExtendedNodeProps } from 'types/models';

import { GridPosition } from '../RoutingGrid/RoutingGrid';
import { useRoutingTree } from '../RoutingTreeContext';
import RiskProfileSelectMenu from '../RoutingSelectMenu/RiskProfileSelectMenu';
import RoutingNode from './RoutingNode';
import { isFirstNode } from '../routingUtils';
import FeatureFlag from 'components/FeatureFlag';
import IncomingArrow from './IncomingArrow';

const RiskProfileNode: React.FC<
  ExtendedNodeProps<{
    isHighlighted: boolean;
    risk_profile_id: string;
    risk_profile_name: string;
    position: GridPosition;
  }>
> = ({ data, id }) => {
  const {
    riskProfiles,
    state: { isHovering },
    actions: { highlightPath },
  } = useRoutingTree();

  const riskProfile = useMemo(() => {
    const { risk_profile_id } = data;

    return riskProfiles.find(
      (riskProfile) => riskProfile.id === risk_profile_id
    );
  }, [data, riskProfiles]);

  return (
    <>
      <FeatureFlag on={isFirstNode(data.position)}>
        <IncomingArrow top="9px" left="-15px" />
      </FeatureFlag>
      <RoutingNode
        onMouseEnter={() => {
          highlightPath({ position: data.position, isHighlighted: true });
        }}
        onMouseLeave={() => {
          highlightPath({ position: data.position, isHighlighted: false });
        }}
        style={{
          opacity: isHovering && !data.isHighlighted ? 0.5 : 1,
        }}
        data-cy={`risk-profile-node-${id}`}
        data-grid-position={`${data.position.col}:${data.position.row}`}
      >
        <Handle
          type="target"
          position={Position.Left}
          isConnectable={false}
          style={{ opacity: 0 }}
        />
        <RiskProfileSelectMenu
          isHighlighted={data.isHighlighted}
          position={data.position}
          riskProfileId={riskProfile?.id ?? data.risk_profile_id}
          riskProfileName={riskProfile?.name ?? data.risk_profile_name}
        />
      </RoutingNode>
    </>
  );
};

export default RiskProfileNode;
