import React from 'react';
import { Box, Text, TextInline } from '@cko/primitives';
import { Handle, Position, NodeProps } from 'react-flow-renderer';
import { lighten } from 'polished';
import styled from 'styled-components';
import useTheme from 'hooks/useTheme';
import { FirstElementSelectMenu } from '../RoutingSelectMenu';
import { GridPosition } from '../RoutingGrid/RoutingGrid';
import IncomingArrow from './IncomingArrow';

interface ExtendedNodeProps<T = any> extends NodeProps {
  data: T;
}

const FirstElementHandles: React.FC = ({ children }) => {
  const theme = useTheme();

  return (
    <Box
      data-cy="add-first-element-node"
      width="170px"
      border="2px dashed"
      borderColor={lighten(0.1, theme.colors.success)}
      p={2}
      borderRadius="3px"
      verticalAlign="middle"
    >
      <Handle
        type="target"
        position={Position.Left}
        isConnectable={false}
        style={{ opacity: 0 }}
      />
      <Handle
        type="source"
        position={Position.Right}
        isConnectable={false}
        style={{ opacity: 0, top: 25 }}
        id={'a'}
      />
      {children}
    </Box>
  );
};

const Wrapper = styled.div`
  top: -20px;
  position: relative;
`;

const AddFirstElementNode: React.FC<
  ExtendedNodeProps<{
    position: GridPosition;
  }>
> = (props) => {
  return (
    <div
      data-cy="add-first-element-routing-node"
      style={{ position: 'relative' }}
    >
      <IncomingArrow left="-13px" top="-2px" />
      <Wrapper>
        <FirstElementHandles>
          <FirstElementSelectMenu position={props.data.position} />
        </FirstElementHandles>
      </Wrapper>
      <Box width="220px" mt={3} style={{ position: 'absolute' }}>
        <Text data-cy="add-first-element-helper-text">
          To begin routing your transactions, let’s start by adding your first
          <TextInline fontWeight="bold"> rule</TextInline>.
        </Text>
      </Box>
    </div>
  );
};

export default AddFirstElementNode;
