import { RoutingNode } from 'types/models/index';
import RoutingGrid from '../RoutingGrid';
import RoutingGridItem from '../RoutingGrid/RoutingGridItem';
import { isTerminatingNodeGuard } from '.';

const getNodeValues = (node: RoutingNode) => {
  switch (node.type) {
    case 'outcome':
      return {
        decision: node.decision,
      };

    case 'rule':
      return {
        rule_id: node.rule_id,
        rule_name: node.rule_name,
      };

    case 'rule_group': {
      return {
        rules: node.rules,
        group_name: node.group_name,
        rule_type: node.rule_type,
      };
    }

    case 'risk_profile': {
      return {
        risk_profile_name: node.risk_profile_name,
        risk_profile_id: node.risk_profile_id,
      };
    }
    case 'machine_learning': {
      return { score_decisions: node.score_decisions };
    }
  }
};

const convertToRoutingGridItem = (node: RoutingNode): RoutingGridItem => {
  const gridItem = new RoutingGridItem(node.type, getNodeValues(node));
  if (!isTerminatingNodeGuard(node)) {
    if (node.true) {
      gridItem.true = convertToRoutingGridItem(node.true);
      gridItem.true.parent = gridItem;
    }

    if (node.false) {
      gridItem.false = convertToRoutingGridItem(node.false);
      gridItem.false.parent = gridItem;
    }
  }

  return gridItem;
};

const addNodesToGrid = (node: RoutingNode, routingGrid: RoutingGrid) => {
  const gridItem = convertToRoutingGridItem(node);
  routingGrid.fromTree(gridItem);

  return gridItem;
};

export const normalizeIntoGrid = (
  tree: RoutingNode,
  routingGrid: RoutingGrid
) => {
  const addDefaultNodesToGrid = (node: RoutingNode) => {
    const firstElementType = 'addFirstElement';
    const firstElement = new RoutingGridItem(firstElementType, {});
    const defaultItem = new RoutingGridItem(tree.type, getNodeValues(tree));

    firstElement.true = defaultItem;
    defaultItem.parent = firstElement;
    routingGrid.fromTree(defaultItem);
  };

  if (tree.type === 'outcome') {
    addDefaultNodesToGrid(tree);
  } else {
    addNodesToGrid(tree, routingGrid);
  }

  return routingGrid;
};

const allowedTypes = [
  'rule',
  'rule_group',
  'risk_profile',
  'outcome',
  'machine_learning',
];

const createRoutingItemPayload = (
  gridItem: RoutingGridItem,
  payload = {}
): RoutingNode => {
  if (!allowedTypes.includes(gridItem.type)) {
    throw new Error(`type: ${gridItem.type} not supported by the API `);
  }

  payload = {
    type: gridItem.type,
    ...gridItem.data,
  };

  if (gridItem.true) {
    payload = {
      ...payload,
      true: {
        ...createRoutingItemPayload(gridItem.true, payload),
      },
    };
  }

  if (gridItem.false) {
    payload = {
      ...payload,
      false: {
        ...createRoutingItemPayload(gridItem.false, payload),
      },
    };
  }

  return payload as RoutingNode;
};

export const denormalizeGrid = (routingGrid: RoutingGrid): RoutingNode => {
  const firstItem = routingGrid.getItem({ row: 0, col: 0 });

  if (!firstItem) {
    throw new Error('No items in the grid');
  }

  if (firstItem.type === 'addFirstElement') {
    if (!firstItem.true) {
      throw new Error('No valid items in the grid');
    }

    return createRoutingItemPayload(firstItem.true);
  }

  return createRoutingItemPayload(firstItem);
};
