import { useReducer, useMemo, useState } from 'react';
import {
  DynamicDecision,
  RiskProfileResponse,
  ScoredRuleState,
} from 'types/models';
import riskProfileReducer, {
  UpdateScoredRuleActionPayload,
  AddScoredRuleActionPayload,
} from './riskProfileReducer';
import useDecisionRanges from 'hooks/useDecisionRanges';

const useRiskProfileState = (riskProfileData: RiskProfileResponse) => {
  const [hasEdited, setHasEdited] = useState(false);
  const initialScoredRuleState: ScoredRuleState = {
    name: riskProfileData.name,
    scored_rules: riskProfileData.scored_rules,
  };

  const [state, dispatch] = useReducer(
    riskProfileReducer,
    initialScoredRuleState
  );

  const {
    state: decisionRangeState,
    actions: decisionRangeActions,
  } = useDecisionRanges(
    riskProfileData.score_decisions as DynamicDecision[],
    setHasEdited
  );

  const actions = useMemo(() => {
    const actions = {
      handleSave: () => {
        setHasEdited(false);
      },
      handleScoredRuleChange: (payload: UpdateScoredRuleActionPayload) => {
        dispatch({
          type: 'UPDATE_SCORED_RULE',
          payload,
        });
        setHasEdited(true);
      },
      handleAddScoredRule: (payload: AddScoredRuleActionPayload) => {
        dispatch({
          type: 'ADD_SCORED_RULE',
          payload,
        });
        setHasEdited(true);
      },
      handleDeleteRiskProfileRule: (payload: AddScoredRuleActionPayload) => {
        dispatch({
          type: 'REMOVE_SCORED_RULE',
          payload,
        });
        setHasEdited(true);
      },
      handleUpdateName: (payload: string) => {
        dispatch({
          type: 'UPDATE_NAME',
          payload,
        });
      },
      ...decisionRangeActions,
    };

    return actions;
  }, [decisionRangeActions]);

  return {
    state: {
      ...state,
      ...decisionRangeState,
      hasEdited,
    },
    actions,
  };
};

export default useRiskProfileState;
