import React from 'react';
import { useRoutingSelectMenuContext } from '../ContextProvider/RoutingSelectMenuContextProvider';
import { Flex, ClickableElement, Text, ListItem } from '@cko/primitives';
import styled, { css } from 'styled-components';
import { IconActionAdd } from '@cko/icons';
import { RoutingListItemWithIndex } from '../NextElementSelectMenu';

interface Props {
  title: string;
  item: RoutingListItemWithIndex;
  index: number;
  showIcon?: boolean;
}

type StyleState = { isActive: boolean; isHighlighted: boolean };

const activeStyles = css(
  ({ theme }) => `
  background-color: ${theme.colors.success};
  color: ${theme.colors.white};
`
);

const highlightedStyles = css(
  ({ theme }) => `
  background-color: ${theme.colors.offWhite};
  color: ${theme.colors.focusLight};
`
);

const getStateStyle = (props: StyleState) => {
  if (props.isActive) {
    return activeStyles;
  }

  if (props.isHighlighted) {
    return highlightedStyles;
  }

  return css`
    background-color: ${({ theme }) => theme.colors.white};
  `;
};

const StyledList = styled(ListItem)`
  cursor: pointer;
  ${getStateStyle}
`;

const RoutingSelectMenuItem: React.FunctionComponent<Props> = ({
  item,
  index,
  title,
  showIcon,
}) => {
  const { getItemProps, highlightedIndex } = useRoutingSelectMenuContext();
  return (
    <StyledList
      {...getItemProps({ item, index })}
      isHighlighted={highlightedIndex === index}
      pl={showIcon ? 0 : 3}
    >
      <ClickableElement>
        <Flex alignItems="center" p={2}>
          {showIcon && <IconActionAdd />}
          <Text textAlign="start" color="base" ml={showIcon ? '2px' : 0}>
            {title}
          </Text>
        </Flex>
      </ClickableElement>
    </StyledList>
  );
};
export default RoutingSelectMenuItem;
