import { useTheme } from 'styled-components';
import {
  PaymentProcessingDecisions,
  Pre3dsDecisions,
  PostAuthDecisions,
} from 'types/models';

export const useRoutingNodeColor = () => {
  const theme = useTheme();

  const nodeColor: Record<PaymentProcessingDecisions, string> = {
    [Pre3dsDecisions.TryExemptions]: theme.colors.success,
    [Pre3dsDecisions.Challenge]: '#4780ec',
    [Pre3dsDecisions.FrictionlessLiability]: '#4780ec',
    [Pre3dsDecisions.NoPreference]: '#4780ec',
    [Pre3dsDecisions.Decline]: theme.colors.danger,
    [PostAuthDecisions.Capture]: theme.colors.success,
    [PostAuthDecisions.Void]: theme.colors.danger,
    [PostAuthDecisions.Flag]: theme.colors.warning,
  };

  return (decision: PaymentProcessingDecisions) => {
    return nodeColor[decision];
  };
};
