import React, { createContext, useContext, useMemo, useState } from 'react';
import RoutingGrid from '../RoutingGrid';
import useRoutingTreeReducer from './../useRoutingTreeReducer';
import { RoutingTreeState } from './../useRoutingTreeReducer/routingTreeReducer';
import { useRoutingv2Data } from 'hooks/api/routing';
import {
  PaymentProcessingStep,
  ScoringRuleSummary,
  RiskProfileSummaries,
  Prompts,
} from 'types/models';
import { normalizeIntoGrid } from '../routingUtils/transformRoutingData';
import { Panel, Flex } from '@cko/primitives';
import Loader from 'components/Loader';
import { RoutingActions } from '../useRoutingTreeReducer/useRoutingTreeReducer';
import useSaveRoutingTree from '../Hooks/useSaveRoutingTree';
import { useRulesData } from 'hooks/api/rules/useRulesData';
import { useRiskProfileSummariesData } from 'hooks/api/riskProfiles';
import { SavePrompt } from 'components/SavePrompts';

export interface RoutingTreeContextType {
  state: RoutingTreeState;
  actions: RoutingActions;
  handleSave: () => Promise<{} | undefined>;
  checkRoutingTree: () => boolean;
  rules: ScoringRuleSummary[];
  riskProfiles: RiskProfileSummaries[];
  step: PaymentProcessingStep;
  isDropdownOpen: boolean;
  setIsDropdownOpen: (value: boolean) => void;
  routingGrid: RoutingGrid;
}

export const RoutingTreeContext = createContext<
  RoutingTreeContextType | undefined
>(undefined);

interface Props {
  step: PaymentProcessingStep;
  onRoutingChange: (hasStateChange: boolean) => void;
}

export const RoutingTreeProvider: React.FunctionComponent<Props> = ({
  children,
  step,
  onRoutingChange,
}) => {
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  const routingGrid = useMemo(() => {
    return new RoutingGrid(20);
  }, []);

  const [state, actions] = useRoutingTreeReducer(routingGrid, onRoutingChange);

  const { isLoading: routingLoading } = useRoutingv2Data(step, (payload) => {
    if (!routingGrid.isEmpty()) {
      return;
    }

    normalizeIntoGrid(payload.routing, routingGrid);
    actions.dataLoaded({
      nodes: routingGrid.createNodes(),
      edges: routingGrid.createEdges(),
    });
  });

  const { data: rulesData, isLoading: rulesLoading } = useRulesData();

  const { handleSave, checkRoutingTree } = useSaveRoutingTree(
    step,
    routingGrid
  );

  const {
    data: riskProfilesData,
    isLoading: riskProfilesLoading,
  } = useRiskProfileSummariesData(step);

  const value = useMemo(() => {
    const rules = rulesData?.summaries ?? [];
    const riskProfiles = riskProfilesData?.summaries ?? [];

    return {
      state,
      actions,
      handleSave,
      checkRoutingTree,
      rules,
      riskProfiles,
      step,
      isDropdownOpen,
      setIsDropdownOpen,
      routingGrid,
    };
  }, [
    actions,
    checkRoutingTree,
    handleSave,
    isDropdownOpen,
    riskProfilesData,
    routingGrid,
    rulesData,
    state,
    step,
  ]);

  const isLoading = routingLoading || rulesLoading || riskProfilesLoading;

  if (isLoading) {
    return (
      <Panel p={3} mb={3}>
        <Flex justifyContent="center" height="300px">
          <Loader size="large" />
        </Flex>
      </Panel>
    );
  }

  return (
    <RoutingTreeContext.Provider value={value}>
      <SavePrompt
        when={state.unsavedChanges}
        promptKey={Prompts.RoutingSavePrompt}
      />
      {children}
    </RoutingTreeContext.Provider>
  );
};

export const useRoutingTree = () => {
  const context = useContext(RoutingTreeContext);

  if (context === undefined) {
    throw new Error('useRoutingTree must be used within a RoutingTreeProvider');
  }

  return context;
};
