import {
  GridPosition,
  EnrichedRoutingTreeNode,
  RoutingTreeNodeInternal,
  RoutingTreeNode,
} from 'types/models';

class RoutingTreeGrid {
  private tree: EnrichedRoutingTreeNode | null;
  private grid: Array<Array<EnrichedRoutingTreeNode | null>>;
  private maxHeight: number;

  constructor() {
    this.tree = null;
    this.grid = [];
    this.maxHeight = 0;
  }

  setTree(tree: RoutingTreeNode) {
    this.tree = this.enrichTree(tree);
    const { grid, maxHeight } = this.getInitialGridAndMaxHeight(this.tree);
    this.grid = grid;
    this.maxHeight = maxHeight;
  }

  getGrid(): Array<Array<RoutingTreeNode | null>> {
    if (this.tree == null) {
      return this.grid;
    }

    this.compressGrid();
    return this.grid;
  }

  private getInitialGridAndMaxHeight(tree: EnrichedRoutingTreeNode) {
    const result = new Array<Array<EnrichedRoutingTreeNode | null>>();
    let maxHeight = 0;

    if (this.tree == null) {
      return { maxHeight, grid: result };
    }

    this.traverseDepthFirst(this.tree, (node, _, height) => {
      maxHeight = Math.max(maxHeight, height);
      const row = new Array<EnrichedRoutingTreeNode | null>(height).fill(null);
      row[height - 1] = node;
      result.push(row);
    });

    return { maxHeight, grid: this.padRows(maxHeight, result) };
  }

  private enrichTree(
    node: RoutingTreeNode,
    parent: EnrichedRoutingTreeNode | null = null,
    height: number = 1,
    getNextIndex: (() => number) | null = null
  ): EnrichedRoutingTreeNode {
    let index = 0;
    if (getNextIndex == null) {
      getNextIndex = () => index++;
    }

    const result: EnrichedRoutingTreeNode = {
      parent: parent,
      dftIndex: getNextIndex(),
      height: height,
      data: node.data,
      children: [],
    };

    result.children = node.children.map((child) =>
      this.enrichTree(child, result, height + 1, getNextIndex)
    );
    return result;
  }

  /***
   * The idea behind compression is to go through the grid, but still treat it as a tree.
   * Concept of shifting cascades to the first children and first grand children.
   *
   * Compressed Grid rules:
   * 1. The first child is always the same level as it's parent
   * 2. A row clash means that there is at least one one node that exists in the same column
   */
  private compressGrid() {
    const leaves = new Array<EnrichedRoutingTreeNode>();

    if (this.tree == null) {
      return this.grid;
    }

    this.traverseDepthFirst(this.tree, (node) => {
      if (!node.children || node.children.length === 0) {
        leaves.push(node);
      }
    });

    leaves.forEach((leaf) => {
      let currNode: EnrichedRoutingTreeNode | null = leaf;

      while (currNode != null) {
        while (this.canBeShiftedUp(currNode)) {
          this.shiftUp(currNode);
        }
        currNode = currNode.parent;
      }
    });

    this.trimGrid();
    return this.grid;
  }

  private shiftUp(node: EnrichedRoutingTreeNode) {
    const nodePosition = this.getGridPosition(node, node.dftIndex);
    const firstChildrenHeight = this.getFirstChildrenHeight(node);
    const numOfColumnsToMerge = nodePosition.column + firstChildrenHeight + 1;

    const base = this.grid[nodePosition.row - 1];
    const toShiftUp = this.grid[nodePosition.row].splice(
      0,
      numOfColumnsToMerge
    );

    this.grid[nodePosition.row - 1] = this.mergeRows(base, toShiftUp);
    this.grid[nodePosition.row] = this.prePad(
      numOfColumnsToMerge,
      this.grid[nodePosition.row]
    );
  }

  private canBeShiftedUp(node: EnrichedRoutingTreeNode) {
    const nodePosition = this.getGridPosition(node, node.dftIndex);
    const firstChildrenHeight = this.getFirstChildrenHeight(node);

    // 1. The first child is always the same level as it's parent
    if (
      this.hasConflict(
        nodePosition.column + firstChildrenHeight,
        nodePosition.row
      )
    ) {
      return false;
    }

    if (
      node.parent &&
      this.getGridPosition(node.parent, nodePosition.row).row ===
        nodePosition.row
    ) {
      return false;
    }

    return true;
  }

  private hasConflict(columnUpperBound: number, row: number) {
    if (row === 0) {
      return true;
    }

    const rowToMerge = this.grid[row].slice(0, columnUpperBound + 1);
    const base = this.grid[row - 1];

    if (rowToMerge.length > base.length) {
      throw new Error('rowToMerge.length > base.length');
    }

    for (let i = 0; i < rowToMerge.length; i++) {
      if (base[i] != null && rowToMerge[i] != null) {
        return true;
      }
    }
    return false;
  }

  // can be optimised using rowMutations and taking snapshot of positional changes
  private getGridPosition(
    node: EnrichedRoutingTreeNode,
    rowUpperBound: number
  ): GridPosition {
    const column = node.height - 1;

    for (let i = rowUpperBound; i >= 0; i--) {
      if (this.grid[i][column] === node) {
        return {
          column,
          row: i,
        };
      }
    }

    throw new Error('node grid position not found');
  }

  private traverseDepthFirst<T extends RoutingTreeNodeInternal<T>>(
    node: T,
    fn: (node: T, parent: T | null, height: number) => void
  ) {
    this.internalTraverseDepthFirst(node, null, 1, fn);
  }

  private internalTraverseDepthFirst<T extends RoutingTreeNodeInternal<T>>(
    node: T,
    parent: T | null = null,
    height: number = 1,
    fn: (node: T, parent: T | null, height: number, index: number) => void
  ) {
    fn(node, parent, height, height);

    if (node.children) {
      node.children.forEach((x) =>
        this.internalTraverseDepthFirst<T>(x, node, height + 1, fn)
      );
    }
  }

  private padRows(
    maxHeight: number,
    grid: Array<Array<EnrichedRoutingTreeNode | null>>
  ) {
    const result = new Array<Array<EnrichedRoutingTreeNode | null>>();
    grid.forEach((row) => {
      if (row.length < maxHeight) {
        while (row.length < maxHeight) {
          row.push(null);
        }
      }
      result.push(row);
    });

    return result;
  }

  private prePad<T>(count: number, arr: Array<T | null>) {
    const result = new Array<T | null>(count).fill(null);
    return result.concat(arr);
  }

  private trimGrid() {
    const result = new Array<Array<EnrichedRoutingTreeNode | null>>();

    for (let i = 0; i < this.grid.length; i++) {
      const row = this.grid[i];
      if (!row.every((c) => c === null)) {
        result.push(row);
      } else {
        break;
      }
    }

    this.grid = result;
  }

  private getFirstChildrenHeight(node: EnrichedRoutingTreeNode) {
    let result = 0;
    let currNode = node;

    while (currNode.children && currNode.children.length > 0) {
      currNode = currNode.children[0];
      result++;
    }

    return result;
  }

  private mergeRows(
    base: Array<EnrichedRoutingTreeNode | null>,
    rowToMerge: Array<EnrichedRoutingTreeNode | null>
  ): Array<EnrichedRoutingTreeNode | null> {
    if (rowToMerge.length > base.length) {
      throw new Error('mergeRows: rowToMerge.length > base.length');
    }

    const result = new Array<EnrichedRoutingTreeNode | null>();

    for (let i = 0; i < base.length; i++) {
      if (i >= rowToMerge.length) {
        result.push(base[i]);
        continue;
      }

      if (rowToMerge[i] != null) {
        result.push(rowToMerge[i]);
        continue;
      }

      if (base[i] != null) {
        result.push(base[i]);
        continue;
      }

      result.push(null);
    }

    return result;
  }
}

export default RoutingTreeGrid;
